
import {emitter} from "../../main";
import Remote from "@/remote/scheduleRequest";
import Vue, {defineComponent} from "vue";
import Dialog from '@/components/dialog'

export default defineComponent({
    name: "SearchEmployee",
    data: function () {
        return {
            // employee list
            searchTerm: '',
            empList: [],
            selectedEmpList: <any>[],
            selectedEmp: null,
            isMobileView: false,
        }
    },
    mounted() {
        if (navigator.userAgent.match(/Android/i)
            || navigator.userAgent.match(/webOS/i)
            || navigator.userAgent.match(/iPhone/i)
            || navigator.userAgent.match(/iPad/i)
            || navigator.userAgent.match(/iPod/i)
            || navigator.userAgent.match(/BlackBerry/i)
            || navigator.userAgent.match(/Windows Phone/i)
        ) {
            console.log('is mobile view true')
            this.isMobileView = true;
        } else {
            this.isMobileView = false;
        }

        this.getEmployeesList()
    },
    watch: {
        searchTerm: function (newVal, oldVal) {
            if (oldVal != newVal) {
                if (newVal.trim().length == 0) {
                    this.empList.forEach(function (emp: any) {
                        emp.classObject.visible = true;
                        emp.classObject.hidden = false;
                    });

                } else {
                    var searchTerm = newVal.toLowerCase().trim();
                    this.empList.forEach((emp: any) => {
                        emp.classObject.visible = (emp.EmployeeName && emp.EmployeeName.toLowerCase().indexOf(searchTerm) > -1) || (emp.EmployeeNo && emp.EmployeeNo.toString().indexOf(searchTerm) > -1);
                        emp.classObject.hidden = !emp.classObject.visible;
                    });
                }
            }
        }
    },
    methods: {
        getEmployeesList() {
            Dialog.loading()
            Remote.EmployeesList().then(response => {
                response.Data.map((sched: any, index: number) => {
                    sched.classObject = {
                        'visible': true,
                        'hidden': false,
                        'selected': false,
                        'odd': index % 2
                    }
                })
                this.empList = response.Data;
            }).finally(() => {
                Dialog.clear()
            })

        },
        toggleEmployeeSelect: function (emp: any) {
            if (emp.classObject.selected) {
                this.removeEmployee(emp)
            } else {
                this.addEmployee(emp)
            }
        },
        removeEmployee: function (emp: any) {
            emp.classObject.selected = false;
            this.selectedEmpList.splice(this.selectedEmpList.indexOf(emp), 1);
            this.$emit('updateSelectedEmpList', this.selectedEmpList)
        },

        addEmployee: function (emp: any) {
            emp.classObject.selected = true;
            this.selectedEmpList.push(emp);
            this.$emit('updateSelectedEmpList', this.selectedEmpList)
        },

        // external
        // called from parent
        clearSelectedEmployeeList: function () {
            this.selectedEmpList.forEach((emp: any) => {
                emp.classObject.selected = false;
            })

            this.selectedEmpList = [];
            this.$emit('updateSelectedEmpList', this.selectedEmpList)
        },
    }
})
