import moment from "moment";

// parse time string and insert column automatic
export function parseTimeColumn(input: string) {
    let parseFormat = 'Hmm';
    if (input.includes(":")) {
        parseFormat = 'H:mm'
    }

    let time = moment(input, parseFormat)
    if (time.isValid()) {
        return time.format("HH:mm")
    } else {
        return 'Invalid Time'
    }
}

export default {
    parseTimeColumn
}