<template>
    <transition name="modal">
        <!--    <div v-if="show">-->
        <div>
            <div v-if="show" class="modal-mask" v-bind:style="[modalIndex ? {'z-index': modalIndex } : '']">
                <div class="modal-wrapper">
                    <div class="modal-backdrop" @click="clickWrapper"></div>
                    <!--          <div class="modal-container">-->
                    <div class="modal-box" v-bind:style="getCustomPosition()" v-bind:class="{'no-modal-box': noBox}">
                        <div class="modal-header">
                            <slot name="header">
                                <!--                default header-->
                            </slot>
                        </div>

                        <div class="modal-body">
                            <slot name="body">
                                <!--                default body-->
                            </slot>
                        </div>

                        <div class="modal-footer">
                            <slot name="footer">
                                <!--                default footer-->
                                <!--                <button class="modal-default-button" @click="$emit('close'); show=false">-->
                                <!--                  OK-->
                                <!--                </button>-->
                            </slot>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!--    </div>-->
    </transition>
</template>

<script lang="ts">
import {defineComponent} from "vue";

export default defineComponent({
    name: "modal",
    props: {
        customPosition: {
            type: Boolean,
            default: false
        },
        top: {
            type: Number,
        },
        left: {
            type: Number
        },
        noBox: {
            type: Boolean
        },
        modalIndex: {
            type: Number,
        }
    },
    data() {
        return {
            show: false
        }
    },
    methods: {
        toggleShow(isShow?: boolean) {
            if (isShow === true || isShow === false) {
                this.show = isShow
            } else {
                this.show = !this.show
            }

        },
        clickWrapper() {
            this.show = false;
            this.$emit('cancel')
        },
        getCustomPosition() {
            if (this.customPosition) {
                // let customPosition = [`position: absolute;`, `top: ${this.top};`, `left: ${this.left};`]
                let customPosition = {
                    position: 'absolute',
                    top: `${this.top}px`,
                    left: `${this.left}px`}
                return customPosition
            } else {
                return ''
            }
        }
    }
})
</script>

<style scoped lang="scss">

@use '../../style/layouts';
@use "../../style/colors";

.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /*background-color: rgba(0, 0, 0, 0.5);*/
    display: table;
    transition: opacity 0.3s ease;
}

.modal-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal-wrapper {
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
}

.modal-box {
    background: white;
    padding: 30px 40px;
    max-width: 940px;
    max-height: 80vh;
    border-radius: 6px;
    box-shadow: colors.$box-shadow-1;
    //$box-shadow-1: 0px 5px 20px rgba(0, 55, 128, 0.2);

    transition: all 0.3s ease;
    z-index: 99999;

    @media screen and (max-width: layouts.$md-screen) {
        max-width: 800px;
    }

    @media screen and (max-width: layouts.$sm-screen) {
        max-width: calc(100% - 40px);
        margin: 0 20px;
    }
}

.no-modal-box {
    background: transparent;
    box-shadow: none;
}

.modal-container {
    position: absolute;
    width: 940px;
    top: 100px;
    bottom: 60px;
    left: calc(50% - 940px / 2);
    margin: 0px auto;
    padding: 30px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    transition: all 0.3s ease;
    font-family: Helvetica, Arial, sans-serif;
    overflow: auto;

    @media screen and (max-width: layouts.$md-screen) {
        width: 800px;
        left: calc(50% - 800px / 2);

    }

    @media screen and (max-width: layouts.$sm-screen) {
        width: auto;
        left: 20px;
        right: 20px;
        top: 50px;
        bottom: 50px;
    }
}

.modal-header h3 {
    margin-top: 0;
    color: #42b983;
}

.modal-body {
    display: flex;
    justify-content: center;
    max-height: calc(80vh - 160px);
    overflow: auto;
}

.modal-footer {
    display: flex;
    justify-content: center;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
    opacity: 0;
}

.modal-leave-active {
    opacity: 0;
}

.modal-enter-active {
    transition: all .3s ease-out;
}

.modal-leave-active {
    transition: all .3s ease-out;
}

.modal-enter-from,
.modal-leave-to {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    opacity: 0;
}

</style>