// import Vue from 'vue'
import {createWebHistory, createRouter, RouteRecordRaw} from 'vue-router'
import mySchedules from "../components/mySchedules/MySchedules.vue";
import SchedulesManagement from "../components/schedulesManagement/SchedulesManagement.vue";
import employeeAllocation from "../components/employeeAllocation/EmployeeAllocation.vue";
import FlexSchedule from "@/components/FlexSchedule/FlexSchedule.vue";
import Availability from "@/components/Availability/Availability.vue";
import Login from "@/components/Login/Login.vue";

// Vue.use(VueRouter)

const routes: Array<RouteRecordRaw> = [
    {
        path: '/login',
        name: 'Login',
        component: Login
    },
    {
        path: '/my-schedules',
        name: 'My Schedule',
        component: mySchedules
    },
    {
        path: '/availability',
        name: 'Availability',
        component: Availability
    },
    {
        path: '/flex-schedule',
        name: 'Flex Schedule',
        component: FlexSchedule
    },

    {
        path: '/team-schedules',
        name: 'Schedule Management',
        component: SchedulesManagement
    },

    {
        path: '/employee-allocations',
        name: 'Employee Allocation',
        component: employeeAllocation
    },
    {
        path: '/',
        name: 'Home',
        component: mySchedules
    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    // base: process.env.BASE_URL,
    routes
})

export default router
