<script>
import Vue from "vue";
import Header from "./header.vue";
import NavBar from "./navbar.vue";
import Footer from "./footer.vue";
import axios from "axios";
import {nextTick} from "@vue/runtime-core";

import './style/dashboard.default.less'
import './style/dashboard.foot.less'
import './style/dashboard.head.less'
import './style/dashboard.nav.less'
import './style/dashboard.nav.tiles.less'

export default {
  props: {
    version: String,
    enableByConfig: Boolean,
    configUrl: String,
    infoUrl: String,
    logoutUrl: String,
    legacyAuth: Boolean
  },
  data() {
    return {
      user: null,
      error: null,
      apps: [
        {
          Enabled: true,
          Name: "Dashboard",
          Url: "",
          Icon: "gauge"
        }
      ],
      loading: true,
      enabled: true,
      showNav: false,
      hideNav: false
    };
  },
  computed: {
    userFullName() {
      return this.user ? `${this.user.FirstName} ${this.user.LastName}` : "";
    },
    logoUrl() {
      let url = this.apps[0].Url;
      return url && this.user
        ? `${url}/Root/Logo?company=${this.user.CompanyNo}&employee=${this.user.EmployeeNo}`
        : "";
    }
  },
  methods: {
    authorize(cb) {
      console.log('dashboard authorize')

        // if (process.env.VUE_APP_LOCAL) {
        if (process.env.VUE_APP_LOCAL1) {
            let getInfoData = {"FirstName":"Norah","LastName":"Barlow","CompanyNo":5004,"EmployeeNo":100603,"UseDashboard":true,"AccessibleMenus":["Work Schedule","Schedule Management","Employee Allocation"]}
            let user = (Affinity.user = this.legacyAuth
                ? {
                    FirstName: getInfoData.Firstname,
                    LastName: getInfoData.Lastname,
                    PayPoint: getInfoData.PayPoint,
                    CompanyNo: getInfoData.CompanyNumber,
                    EmployeeNo: getInfoData.EmployeeNumber
                }
                : getInfoData);

            this.enabled = !this.enableByConfig || user.UseDashboard;

            if (cb) cb();
        } else {
            let remoteConfig = {
                method: process.env.VUE_APP_AXIOSHOST ? 'get' : 'post',
                url: this.legacyAuth ? Affinity.authroot + "/GetUser" : this.infoUrl
                // url: 'http://localhost:3000/Auth/GetInfo'
            }

            axios(remoteConfig)
                // axios.get(url)
                .then(resp => {
                    let user = (Affinity.user = this.legacyAuth
                        ? {
                            FirstName: resp.data.Firstname,
                            LastName: resp.data.Lastname,
                            PayPoint: resp.data.PayPoint,
                            CompanyNo: resp.data.CompanyNumber,
                            EmployeeNo: resp.data.EmployeeNumber
                        }
                        : resp.data);

                    this.enabled = !this.enableByConfig || user.UseDashboard;

                    if (cb) cb();
                })
                .catch((err) => {
                    console.log('err', err)
                    this.error = "Unable to load the page.";
                    this.loading = false;
                });

        }



    },
    getConfig() {
      let remoteConfig = {
        method: process.env.VUE_APP_AXIOSHOST ? 'get' : 'post',
        url: this.configUrl
      }

      axios(remoteConfig)
        // .get(this.configUrl)
        .then(resp => {
          let config = resp.data;

          this.user = Affinity.user;

          this.apps[0].Url = config.Url;

          if (config.Apps) {
            config.Apps.forEach(app => {
              this.apps.push(app.Link);
            });
          }

          if (config.Forms && this.user.EmployeeNo <= 5000000) {
            config.Forms.forEach(form => {
              if (form.Enabled) this.apps.push(form);
            });
          }

          this.loading = false;

          nextTick(() => {
            this.$emit("ready", config);
          });
        })
        .catch(() => {
          this.error = "Unable to load the page.";
        })
        .then(() => {
          this.loading = false;
        });
    },
    reloadPage() {
      window.location.reload();
    },
    onNavToggle(mobile) {
      if (mobile) this.showNav = !this.showNav;
      else this.hideNav = !this.hideNav;
    },
    onNavHide() {
      this.showNav = false;
    }
  },
  created() {
    console.log('dashboard created')
    this.authorize(() => {
      if (this.enabled) {
        this.getConfig();
      } else {
        this.loading = false;
      }
    });
  },
  components: {
    "dash-header": Header,
    navbar: NavBar,
    "dash-footer": Footer
  }
};
</script>

<template>
  <div :class="{ dashboard: enabled, 'show-nav': showNav, 'hide-nav': hideNav }">
    <div v-if="loading" class="UIPromtBoxBG">
      <div class="UIPromtBox loader">
        <span class="img-loader"></span>
        <span class="content aligncenter">Please wait</span>
      </div>
    </div>
    <div v-else-if="error" class="UIPromtBoxBG">
      <div class="UIPromtBox error-text">
        <span>{{ error }}</span>
        <br />
        <a href="javascript:void(0)" @click="reloadPage">Please retry</a>
      </div>
    </div>
    <div v-else class="pagewrapper">
      <dash-header :title="userFullName" :logoUrl="logoUrl" @navtoggle="onNavToggle"></dash-header>
      <navbar :apps="apps" :logout-url="logoutUrl" @hide="onNavHide"></navbar>
      <div class="pagecontent">
        <div class="contentwrapper">
          <slot></slot>
        </div>
      </div>
      <dash-footer :version="version"></dash-footer>
    </div>
  </div>
</template>

<style lang="scss">
.dashboard {
  height: 100%;

  .UIPromtBox.error-text {
    a {
      color: #90caf9;
      margin-top: 5px;
      display: inline-block;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}
</style>
