
import {defineComponent} from "vue";

export default defineComponent({
    name: "modal",
    props: {
        customPosition: {
            type: Boolean,
            default: false
        },
        top: {
            type: Number,
        },
        left: {
            type: Number
        },
        noBox: {
            type: Boolean
        },
        modalIndex: {
            type: Number,
        }
    },
    data() {
        return {
            show: false
        }
    },
    methods: {
        toggleShow(isShow?: boolean) {
            if (isShow === true || isShow === false) {
                this.show = isShow
            } else {
                this.show = !this.show
            }

        },
        clickWrapper() {
            this.show = false;
            this.$emit('cancel')
        },
        getCustomPosition() {
            if (this.customPosition) {
                // let customPosition = [`position: absolute;`, `top: ${this.top};`, `left: ${this.left};`]
                let customPosition = {
                    position: 'absolute',
                    top: `${this.top}px`,
                    left: `${this.left}px`}
                return customPosition
            } else {
                return ''
            }
        }
    }
})
