<template>
    <div v-if="editing" class="d-flex flex-wrap justify-center" v-bind:class="{'input-width-full': fullWidthInput}">
        <label>{{ label }}</label>
        <input v-model="value" class="input-width"/>
        <!--    <div class="d-flex">-->
        <button class="ml-1 btn-xsmall blue" @click="update">
            <span class="icon-tick "></span>
        </button>
        <button class="ml-1 btn-xsmall orange" @click="cancel">
            <span class="icon-cross"></span>
        </button>
        <!--    </div>-->

    </div>
<!--    <div v-else @click="changeEditing(true)">-->
    <div v-else>
        <label>{{ label }}</label>
        <slot name="displayValue">{{ modelValue }}</slot>
    </div>

</template>

<script lang="ts">
import {defineComponent} from "vue";

export default defineComponent({
    name: "EditableCellElement",
    props: {
        modelValue: {
            type: [String, Number]
        },
        label: {
            type: String
        },
        fullWidthInput: {
            type: Boolean
        }
        // editing: {
        //   type: Boolean
        // }
    },
    data() {
        return {
            // edit
            value: '',
            editing: false
        }
    },
    methods: {
        changeEditing() {
            this.value = this.modelValue as string;
            this.editing = true;
            this.$emit("updateEditing", true)
        },
        update() {
            this.$emit("update:modelValue", this.value)
            this.editing = false;
        },
        cancel() {
            this.editing = false
            this.$emit("updateEditing", false)
        }

    }
})
</script>

<style scoped lang="scss">
@use "../../style/colors";

.input-width {
    width: 30px;
}

.input-width-full {
    .input-width {
        width: 120px;
    }

    label {
        width: 120px;
    }
}

.btn-xsmall {
    background: white;
    box-shadow: none;
    border-radius: 5px;
    padding: 1px 4px;
    line-height: 0;

    &.blue {
        color: colors.$aff-blue;
        border: 1px solid colors.$aff-blue;
    }

    &.orange {
        color: colors.$orange;
        border: 1px solid colors.$orange;
    }
}

</style>