
import {defineComponent} from "vue";

export default defineComponent({
    name: "EditableCellElement",
    props: {
        modelValue: {
            type: [String, Number]
        },
        label: {
            type: String
        },
        fullWidthInput: {
            type: Boolean
        }
        // editing: {
        //   type: Boolean
        // }
    },
    data() {
        return {
            // edit
            value: '',
            editing: false
        }
    },
    methods: {
        changeEditing() {
            this.value = this.modelValue as string;
            this.editing = true;
            this.$emit("updateEditing", true)
        },
        update() {
            this.$emit("update:modelValue", this.value)
            this.editing = false;
        },
        cancel() {
            this.editing = false
            this.$emit("updateEditing", false)
        }

    }
})
