// import 'babel-polyfill'
// import Vue from 'vue'
import {createApp} from 'vue'
import App from './App.vue'
import router from './router'
// import store, {key} from './store'
import store from './store'
// import vuetify from './plugins/vuetify';
// import Dashboard from '../external-lib/dashboard/index.vue'
// import './components/directive';
import './assets/fonts/ui.font.icons.scss';
import './style/index.scss';
import moment from 'moment'
import mitt, {Emitter} from "mitt";
import VTooltip from 'v-tooltip';
import customFilters from "@/plugins/filters";
import iconifyIcons from './plugins/iconifyIcons';
import companyConfig from "@/plugins/companyConfig";

export const emitter: Emitter = mitt();


// Vue.use(VTooltip, {
//     defaultBoundariesElement: 'window'
// })

// VTooltip.enabled = window.innerWidth > 768


navigator.serviceWorker.getRegistrations().then(function (registrations) {
    for (let registration of registrations) {
        registration.unregister()
    }
})

let app = createApp(App)
app.config.globalProperties.emitter = emitter
app.use(VTooltip)
app.use(companyConfig)
// app.use(store, key).use(router).mount('#app')
app.use(store).use(router).mount('#app')



app.config.globalProperties.$filters = customFilters
app.config.globalProperties.$icons = iconifyIcons

