

<template>
    <div>
        <div class="heading"></div>
        <div class="content">
            <div class="clock-frame">
                <canvas ref="clockRef" width="200" height="200" style="background-color:white"></canvas>
            </div>
            <div class="controls"></div>
        </div>
    </div>
</template>

<script lang="ts">
// todo: click and choose time
// todo: data transfer
import {defineComponent, onMounted, ref} from "vue";

export default defineComponent({
    name: "TimePicker",
    // data() {
    //   return {
    //     clockHours: [],
    //     clockMinutes: [],
    //   }
    // },
    setup() {
        const clockHours = ref<number[]>([]);
        const clockMinutes = ref<number[]>([]);
        const clockRef = ref<any>(null)

        for (let i = 0; i < 12; i++) {
            clockHours.value.push(i)
        }


        for (let i = 0; i <= 55; i = i + 5) {
            clockMinutes.value.push(i)
        }

        onMounted(() => {
            // the DOM element will be assigned to the ref after initial render
            let canvas = clockRef.value;
            let ctx = canvas.getContext('2d')
            let radius = canvas.height / 2
            ctx.translate(radius, radius);
            radius = radius * 0.9
            drawClock();


            function drawClock() {
                ctx.arc(0, 0, radius, 0, 2 * Math.PI);
                ctx.fillStyle = "lightGrey";
                ctx.fill();

                drawFace(ctx, radius);
                drawNumbers(ctx, radius);
                drawTime(ctx, radius);
            }

            function drawFace(ctx: any, radius: number) {
                ctx.beginPath();
                ctx.arc(0, 0, 5, 0, 2 * Math.PI);
                ctx.fillStyle = '#333';
                ctx.fill();
            }

            function drawNumbers(ctx: any, radius: number) {
                let ang, num;
                ctx.font = "18px arial";
                ctx.textBaseline = "middle";
                ctx.textAlign = "center";

                for (let num = 1; num < 13; num++) {
                    ang = num * Math.PI / 6;
                    ctx.rotate(ang);
                    ctx.translate(0, -radius * 0.85);
                    ctx.rotate(-ang);
                    ctx.fillText(num.toString(), 0, 0);
                    ctx.rotate(ang);
                    ctx.translate(0, radius * 0.85);
                    ctx.rotate(-ang);
                }
            }

            function drawTime(ctx: any, radius: number) {
                var now = new Date();
                var hour = 5;
                var minute = 15;
                //hour
                hour = hour % 12;
                hour = (hour * Math.PI / 6);
                drawHand(ctx, hour, radius * 0.5, 5);
                //minute
                minute = (minute * Math.PI / 30);
                drawHand(ctx, minute, radius * 0.7, 3);
            }

            function drawHand(ctx: any, pos: any, length: number, width: number) {
                ctx.beginPath();
                ctx.lineWidth = width;
                ctx.lineCap = "round";
                ctx.moveTo(0, 0);
                ctx.rotate(pos);
                ctx.lineTo(0, -length);
                ctx.stroke();
                ctx.rotate(-pos);
            }
        })

        return {
            clockHours, clockMinutes, clockRef
        }

    },
    // mounted() {
    //   console.log('ref', this.$refs.clockRef)
    //   let clockRef = this.$refs.clockRef;
    //
    // }
})
</script>

