<template>
    <div class="day-info">
        <!--    <div class="display-day" @click="editScheDay(scheDay)" v-if="!scheDay.editing">-->
        <div class="display-day">

            <!--      start time and end time -->
            <div>
<!--                <div v-if="!editTime && (scheDay.startTime || scheDay.endTime)" @click="updateEditTime(true)">-->
                <div v-if="!editTime && (scheDay.startTime || scheDay.endTime)" >
                    <div>{{ scheDay.startTime }} - {{ scheDay.endTime }}</div>
                </div>
                <div v-else-if="editTime">
                    <div class="d-flex flex-column align-center">
                        <label>Start Time</label>
                        <input class="time-input" v-model="scheDay.startTime">
                    </div>
                    <div class="mt-2 d-flex flex-column align-center">
                        <label class="px-2">End Time</label>
                        <input class="time-input" v-model="scheDay.endTime">
                    </div>
                    <button class="mt-2" @click="editTime = false; updateEditing(false)">Confirm</button>
                </div>
                <!--      Cell without content -->
                <div v-else class="create-day" v-bind:class="{'create-day-show': !scheDay.time && !scheDay.schedule}">
                    <button class="blue small" @click="createSche(scheDay)"><span class="white">Create</span></button>
                </div>
            </div>
            <div v-if="!editTime">
                <editable-cell-element v-if="cellSettings.showHours.value && scheDay.hours"
                                       label="Net hours:" :modelValue="scheDay.hours"

                                       @update:modelValue="scheDay.hours = $event; updateEditing"></editable-cell-element>
                <div v-if="scheDay.scheduleDetail">
                    <editable-cell-element v-if="cellSettings.showBreakLength.value"
                                           label="Break Length:" :modelValue="scheDay.scheduleDetail.breakLength"

                                           @update:modelValue="scheDay.scheduleDetail.breakLength = $event; updateEditing"></editable-cell-element>
                    <editable-cell-element v-if="cellSettings.showShiftId.value"
                                           label="Shift ID:" :modelValue="scheDay.scheduleDetail.shiftId"

                                           @update:modelValue="scheDay.scheduleDetail.shiftId = $event; updateEditing">
                        <template v-slot:displayValue>

                        </template>
                    </editable-cell-element>
                    <div v-if="cellSettings.showPosition.value">
                        <div>Position:</div>
                        <div>{{scheDay.scheduleDetail.positionTitle}}</div>
                    </div>
<!--                    <editable-cell-element v-if="cellSettings.showPosition.value"-->
<!--                                           label="Position:" :modelValue="scheDay.scheduleDetail.positionTitle"-->
<!--                                           :fullWidthInput="true"></editable-cell-element>-->
<!--                    <editable-cell-element class="mt-1" v-if="cellSettings.showCostElement1.value"-->
<!--                                           label="CostElement1:" :modelValue="scheDay.scheduleDetail.costElement1"-->
<!--                                           :fullWidthInput="true"-->
<!--                                           @startedEditing="editing = true"-->
<!--                                           @update:modelValue="scheDay.scheduleDetail.costElement1 = $event; updateEditing"></editable-cell-element>-->
<!--                    <editable-cell-element class="mt-1" v-if="cellSettings.showCostElement2.value"-->
<!--                                           label="CostElement2:" :modelValue="scheDay.scheduleDetail.costElement2"-->
<!--                                           :fullWidthInput="true"-->
<!--                                           @startedEditing="editing = true"-->
<!--                                           @update:modelValue="scheDay.scheduleDetail.costElement2 = $event; updateEditing"></editable-cell-element>-->
                </div>
                <!--      <div>{{ scheDay.schedule }}</div>-->
            </div>
            <div v-if="scheDay.leaveUnit" class="leave-unit mt-2">
                <span v-if="scheDay.leaveUnit.calcUnit === 'D'">Leave: {{scheDay.leaveUnit.daysAppliedFor}} day</span>
                <span v-else >Leave: {{scheDay.leaveUnit.hoursAppliedFor}} hours</span>
            </div>
            <div v-if="scheDay.unAvailability" class="leave-unit mt-2">
                <div>Unavailability:</div>
                <span>{{scheDay.unAvailability.startTime}} - {{scheDay.unAvailability.endTime}}</span>
            </div>
            <div v-if="!editing && (scheDay.startTime || scheDay.endTime)">
                <button class=" detail-button blue small mt-2" @click="moreDetail(scheDay)">
                    <span class="white caption">Edit</span>
                </button>
            </div>

        </div>
    </div>
</template>

<script lang="ts">
import {ScheduleDay} from "@/components/FlexSchedule/schedule-day-mock";
import {computed, defineComponent, PropType, reactive} from "vue";
import {FlexScheduleCellSetting, FlexScheduleMutation} from "@/store/flexScheduleModule";
import {useStore} from "vuex";
import EditableCellElement from "@/components/FlexSchedule/EditableCellElement.vue";
import {LookupActions, ShiftType} from "@/store/lookupModule";

export default defineComponent({
    name: "DynamicScheCell",
    components: {
        EditableCellElement
    },
    props: {
        scheDay: {
            type: Object as PropType<ScheduleDay>
        }
    },
    data() {
        return {
            editTime: false,
            editing: false,
        }

    },
    setup() {
        const store = useStore()

        const cellSettings: FlexScheduleCellSetting = store.state.FlexSchedule.flexScheduleCellSetting

        const shiftOptions = computed(() => {
                return store.state.LookUp.shifts
            })

        const positionOptions = computed(() => {
            return store.state.LookUp.positions
        })


        return {cellSettings,
                shiftOptions,
                positionOptions,
                selectedShift: computed((shiftId: string) => {
                    return shiftOptions.value.find((option: ShiftType) => {
                        return option.shiftType === shiftId
                    })
                }),
                // positionOptions: computed(() {
                //     let options = this.$store.state.LookUp.positions;
                //     if (options.length === 1) {
                //         (this as any).selectedPosition = options[0];
                //     }
                //     return options
                // }),
        }
    },
    methods: {
        updateElement($event: any, scheDay: any, key: any) {
            // console.log('update event', $event)
            // console.log('update target', scheDay)
            // console.log('update key', key)
            scheDay.startTime = $event;
        },
        moreDetail(day: ScheduleDay) {
            // console.log('day', day)
            this.$store.commit(FlexScheduleMutation.setScheduleDay, day)
            this.$emit('openEditDialog', true)
        },

        updateEditTime(isEditing: boolean) {
            this.editTime = isEditing;
            if (isEditing) {
                this.editing = true;
            }
        },

        createSche(scheDay: ScheduleDay) {
            // this.$store.commit(FlexScheduleMutation.setTargetEmployee)
            this.$store.dispatch(LookupActions.getPositions, (scheDay as any).employeeNo)
            this.$store.commit(FlexScheduleMutation.createNewScheduleDay, scheDay)
            this.$emit('openEditDialog', false)
        },

        updateEditing(isEditing?: boolean) {
            if (isEditing === true || isEditing === false) {
                this.editing = isEditing;
            } else {
                this.editing = !this.editing
            }

        },
        startEditing() {
            this.editing = true;
        }

        // confirmEdit(day: any) {
        //   day.editing = false;
        // },
        //
        // cancelEdit(day: any) {
        //
        // },
        //
        // editScheDay(day: any) {
        //   day.editing = true
        // }
    }
})
</script>

<style scoped lang="scss">
.day-info {
    //min-width: 100px;
    //min-height: 30px;
    width: 100%;
    height: 100%;
}

.display-day {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 10px;


    .create-day {
        display: none;
        transition: opacity linear 0.15s
    }

    .create-day-show {
        display: block;
        opacity: 0;
    }

    .detail-button {
        position: absolute;
        bottom: 0;
        right: 0;
        display: none;
    }

    &:hover .create-day-show {
        opacity: 1;
    }

    &:hover .detail-button {
        display: block;
    }
}

.leave-unit {
    width: 100%;
    padding: 2px 5px;
    background-color: #E0E0E0
}

.time-input {
    max-width: 120px;
}
</style>