<template>
    <div class="custom-select" tabindex="1"  ref="selectRef">
        <div class="custom-select-selected">
            <input class="custom-select-input" ref="selectInputRef"
                   v-model="selectSearchInput" @keyup="updateSearchList">
        </div>
        <div class="custom-select-items">
            <div class="custom-select-item" ref="selectDropdownRef"
                 v-for="(option, index) in dropDownList" :key="index"
                 :tabindex="index + 99"
                 v-bind:class="{'targeting': option.targeting}"
                 @click="chooseOption($event, option)">
                <div class="cell-config">
                    {{ option.name }}
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import {defineComponent, ref} from "vue";
import _ from "lodash";

export default defineComponent({
name: "AutoComplete",
    props: {
        inputList: {
            type: Array
        },
        optionName: {
            type: String
        }
    },
    data() {
        return {
            // selectSearchInput: false,
            editing: false,
            targetingIndex: <number | null>null,
            // downDownList: <any[]>[],
        }

    },
    setup(props: any) {

        if (!props.optionName) {
            // todo: treat as string array
            console.error("need object, optionName is missing")
        } else {
            // todo: treat as object array
            // console.log('inputLIst', props.inputList)
            // console.log('inputLIst', props.optionName)
        }

        const originList = ref(props.inputList.map((obj: any, index: number) => {
            return {
                targeting: false,
                name: obj[props.optionName],
                index: index
            }
        }))
        const dropDownList = ref(originList.value.slice())
        const selectSearchInput = ref('')

        return {originList, dropDownList, selectSearchInput}

    },

    methods: {
        updateSearchList(event: any) {
            if (event.key === "ArrowDown") {
                this.moveTargetDown();
                return
            } else if (event.key === "ArrowUp") {
                this.moveTargetUp();
                return
            } else if (event.key === "Enter") {
                if (this.targetingIndex) {
                    this.selectFromTarget();
                    return
                }
            }

            this.dropDownList = this.originList.filter((o: any) => {
                o.targeting = false;
                return o.name.toLowerCase().indexOf(this.selectSearchInput.toLowerCase()) > -1
            })
        },

        moveTargetDown() {
            if (this.targetingIndex !== null) {
                this.dropDownList[this.targetingIndex].targeting = false;
                if (this.targetingIndex + 1 < this.dropDownList.length) {
                    this.targetingIndex++
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else if (this.targetingIndex + 1 === this.dropDownList.length) {
                    this.targetingIndex = 0
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else {
                    this.targetingIndex = 0
                    this.dropDownList[this.targetingIndex].targeting = true;
                }
            } else {
                this.targetingIndex = 0
                this.dropDownList[this.targetingIndex].targeting = true;
            }
        },

        moveTargetUp() {
            if (this.targetingIndex !== null) {
                this.dropDownList[this.targetingIndex].targeting = false;
                if (this.targetingIndex - 1 >= 0) {
                    this.targetingIndex--
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else if (this.targetingIndex - 1 < 0) {
                    this.targetingIndex = this.dropDownList.length - 1;
                    this.dropDownList[this.targetingIndex!].targeting = true;
                } else {
                    this.targetingIndex = 0
                    this.dropDownList[this.targetingIndex].targeting = true;
                }
            } else {
                this.targetingIndex = 0
                this.dropDownList[this.targetingIndex].targeting = true;
            }
        },

        selectFromTarget() {
            this.selectSearchInput = this.dropDownList[this.targetingIndex!].name
            this.$emit('selected', this.inputList![this.targetingIndex!])
        },

        chooseOption($event: any, option: any) {
            this.selectSearchInput = option.name
            this.$emit('selected', this.inputList![option.index])

            $event.target.blur();
        }
    }
})
</script>

<style lang="scss" scoped>
@use '../../style/colors';

.custom-select {
    font-size: 12px;
}

.custom-select-selected {
    min-height: 24px;
    padding: 4px 1.5em 4px 0.2em;
    border: none;
    border-radius: 0;
    background-color: transparent;
    border-bottom: 1px solid rgba(colors.$grey, 0.7);

}

.custom-select-selected:after {
    top: 8px;
    right: 0;
}

.custom-select:focus-within {
.custom-select-selected {
    border: none;
    border-radius: 0;
    border-bottom: 1px solid colors.$aff-dark-blue;
}

.custom-select-selected:after {
    transform: rotate(180deg);
    top: 4px;
}
}

.custom-select-items {
    background: white;
}

.custom-select-item {
    color: colors.$dark-blue-app;
// padding-left: 1em;
    cursor: pointer;
    user-select: none;
    padding: 4px 1em;
}

.custom-select-item:hover {
    color: colors.$aff-dark-blue;
    background-color: lighten(colors.$aff-blue, 50%);
}

.custom-select-input {
    border: none;
    background: transparent;
}

.cell-config {
    pointer-events: none;
}

.targeting {
    background-color: colors.$aff-dark-blue;
    color: white;
}
</style>