<template>
    <div class="d-flex mt-4 justify-space-between">

        <div class="ml-2">
            <flex-schedule-cell-control></flex-schedule-cell-control>
        </div>
        <div class="weekDetail d-flex align-center">
            <span class="arrow-height mr-4" @click="getLastWeek">
                <Icon :icon="$icons.chevronLeft" height="30" :style="{ color: '#0000008a' }" ></Icon>
            </span>
            <span @click="chooseCalendarDate">{{$filters.ordinalSuffix(dayFocus.week())}} Week, {{ dayFocus.format('DD/MM/YYYY') }}</span>
            <span class="arrow-height ml-4" @click="getNextWeek">
                <Icon :icon="$icons.chevronRight" height="30" :style="{ color: '#0000008a' }"></Icon>
            </span>

        </div>
        <div class="d-flex align-center status-section">
            <div v-if="weekStatus !== 'published'">
                <div>
                    <span>
                        <div class="custom-select" tabindex="1" ref="selectRef">
                            <div>
                                 <button class="grey small copy2 white--text"  @click="changeDisplayControl">
                                    <span class="inline-block-middle">Actions</span>
                                    <span class="inline-block-middle ml-1" style="height: 15px">
                                        <Icon :icon="$icons.chevronDown" height="15" :style="{ color: '#FFFF' }"></Icon>
                                    </span>
                                </button>
                            </div>
                            <div class="custom-select-items" v-bind:style="{ 'left': left + 'px'}">
                                <div class="action-buttons">
                                    <button class="blue small copy2 white--text"  @click="openDraftDialog">Copy</button>
                                    <button class="blue small copy2 mt-2 white--text" v-if="weekStatus === 'draft'" @click="publishCurrentWeek">Publish</button>
                                </div>
                            </div>
                        </div>
                    </span>
                </div>
            </div>

            <span class="copy2 ml-2 text-capitalize">
                Status: {{weekStatus === 'published' ? 'published' : 'draft' }}
            </span>
        </div>
    </div>

    <div class="schedules-data-medium mt-4" style="overflow:auto;">
        <div class="flex-sche-table">
            <div class="table-head">
                <div class="schedule-head-grid">
                    <div class="table-cell"></div>
                    <div class="table-cell">Std Hours</div>
                    <div class="table-cell">Net Hours</div>
                    <div class="table-cell" v-for="weekDay in weekDays">
                        {{ weekDay.day }}
                    </div>
                </div>
            </div>
            <div class="table-body">
<!--                <div v-for="(scheduleArr, value) of scheduleWeek.entries()">-->
<!--                    <div>{{scheduleArr}}</div>-->
<!--                    <div>{{value}}</div>-->
<!--                </div>-->
                <div class="schedules-arr" v-for="(empNo) in scheduleWeek.keys()">
                    <div class="content-cell content-employee-name" @click="openEmployeeDetail(empNo)">{{ scheduleWeek.get(empNo).EmployeeName }}</div>
                    <div class="content-cell">{{ scheduleWeek.get(empNo).StdHoursPerWeek.toFixed(2) }}</div>
                    <div class="content-cell">{{getTotalHour(scheduleWeek.get(empNo).sche)}}</div>
                    <div class="content-cell" v-for="scheDay in scheduleWeek.get(empNo).sche" v-bind:style="scheDay.column">
                        <dynamic-day-cell :scheDay="scheDay" @openEditDialog="openEditDialog"></dynamic-day-cell>
                    </div>
                </div>
            </div>
        </div>

    </div>

<!--    popups -->
    <div>
        <edit-flex-schedule ref="EditModalRef" :isEdit="isEditingDialog" :weekStatus="weekStatus" :modalIndex="1999" @reload="reloadAfterEdit"></edit-flex-schedule>
        <date-picker ref="DatePickerRef" :top="datePickerTop" :left="datePickerLeft"></date-picker>
        <draft-creation-dialog ref="DraftCreationDialogRef" :modalIndex="1999" @reload="reloadAfterEdit"></draft-creation-dialog>
        <extra-emp-detail ref="ExtraEmpDetailRef" :externalDetail="externalDetail"></extra-emp-detail>
    </div>
</template>

<script lang="ts">
import {defineComponent, ref} from "vue";
import moment, {Moment} from "moment";
import {ScheduleDay} from "./schedule-day-mock";
import DatePicker from "@/components/common/DatePicker.vue";
import EditFlexSchedule from "@/components/FlexSchedule/EditFlexSchedule.vue";
import DynamicDayCell from "@/components/FlexSchedule/DynamicDayCell.vue"
import {FlexScheduleMutation} from "@/store/flexScheduleModule";
import FlexScheduleCellControl from "@/components/FlexSchedule/FlexScheduleCellControl.vue";
import TimePicker from "@/components/common/timePicker/TimePicker.vue";

import {Icon} from '@iconify/vue';
import AutoComplete from "@/components/common/AutoComplete.vue";
import Remote from '@/remote/scheduleRequest'
import {LookupActions} from "@/store/lookupModule";
import DraftCreationDialog from "@/components/FlexSchedule/DraftCreationDialog.vue";
import Dialog from "@/components/dialog";
import {EmployeeActions} from "@/store/employeeModule";
import {
    CFRemoteData,
    FlexScheduleRequest,
    FlexScheduleRequestCap
} from "@/components/FlexSchedule/FlexScheduleInterface";
import ExtraEmpDetail from "@/components/FlexSchedule/ExtraEmpDetail.vue"

declare var Affinity: any

export enum WeeklyPublishStatus {
    empty = '',
    draft = 'draft',
    published = 'published'
}

export default defineComponent({
    name: "FlexSchedule",
    components: {
        DraftCreationDialog,
        DatePicker,
        TimePicker,
        EditFlexSchedule,
        DynamicDayCell,
        FlexScheduleCellControl,
        Icon,
        AutoComplete,
        ExtraEmpDetail
    },
    data() {
        return {
            dayFocus: moment(),
            weekDays: <any[]>[], // calendar week days name
            scheduleWeek: <any[]>[], // list of data per week
            selectedWeekStart: moment(),
            datePickerTop: 0,
            datePickerLeft: 0,
            employeeList: <any>[],
            scheduleWeekMap: new Map(),
            weekStatus: WeeklyPublishStatus.empty,

            //
            showControl: false,
            extraEmpDetail: []

        }
    },
    setup() {
        // const store = useStore()
        // store.dispatch(FlexScheduleAction.loadTargetSchedule,6)
        const DraftCreationDialogRef = ref<InstanceType<typeof DraftCreationDialog>>();
        const EditModalRef = ref<InstanceType<typeof EditFlexSchedule>>();
        const ExtraEmpDetailRef = ref<InstanceType<typeof ExtraEmpDetail>>();
        const isEditingDialog = ref(false);
        const externalDetail = ref(<CFRemoteData[]>[])

        function openDraftDialog() {
            DraftCreationDialogRef.value!.toggleShow()
        }

        function openEditDialog(isEdit: false) {
            isEditingDialog.value = isEdit;
            EditModalRef.value!.openEdit();
        }

        function openEmployeeDetail(employeeNo: number) {
            Dialog.loading()
            Remote.GetEmployeeExtraDetail(employeeNo).then((res: CFRemoteData[]) => {
                externalDetail.value = res.map((data: CFRemoteData) => {
                    if (data.InputType === 'DateTime') {
                        data.remoteKeyValue = data.KeyValue
                        data.remoteValue = data.Value
                        data.KeyValue = moment(data.KeyValue).format('ddd DD MMM YYYY')
                        data.Value = moment(data.Value).format('ddd DD MMM YYYY')
                    }
                    return data
                })
                Dialog.clear()
                ExtraEmpDetailRef.value!.toggleShow();
            }, (e) => {
                Dialog.clear()
                Dialog.alert('Something went wrong')
            })
        }

        return {openDraftDialog, openEditDialog, openEmployeeDetail,
            DraftCreationDialogRef, EditModalRef, ExtraEmpDetailRef,
            isEditingDialog, externalDetail}
    },
    async mounted() {
        this.$store.dispatch(LookupActions.getPositions, Affinity.user.EmployeeNo)
        this.$store.dispatch(LookupActions.getShifts)
        this.$store.dispatch(LookupActions.getCostElement)

        let weekStart = this.dayFocus.clone().startOf('week')
        let weekEnd = this.dayFocus.clone().endOf('week')
        this.loadDate(weekStart, weekEnd)
    },

    methods: {

        reloadAfterEdit() {
            let weekStart = this.dayFocus.clone().startOf('week')
            let weekEnd = this.dayFocus.clone().endOf('week')
            this.loadDate(weekStart, weekEnd)
        },

        async loadDate(weekStart: Moment, weekEnd: Moment) {
            this.weekStatus = WeeklyPublishStatus.empty;
            this.showControl = false;

            let param = {
                managerEmployeeNo: Affinity.user.EmployeeNo,
                dateFrom: weekStart.format('YYYY-MM-DD'),
                // dateFrom: '2020-01-01',
                dateTo: weekEnd.format('YYYY-MM-DD'),
                // dateTo: '2021-01-01',
            }

            Dialog.loading()
            let [dateData, leaves, employeeList] = await Promise.all([
                this.getDateData(weekStart, weekEnd),
                Remote.GetManagedLeaves(param),
                this.$store.dispatch(EmployeeActions.getEmployees, Affinity.user.EmployeeNo).then((res: any) => {
                    return res.map((data: any) => {
                        return {
                            EmployeeName: data.commonName,
                            EmployeeNo: data.employeeNo,
                            StdHoursPerWeek: data.stdHoursPerWeek,
                            PositionCode: data.positionCode
                        }
                    })
                })
            ]);
            Dialog.clear()

            for (let employee of employeeList) {
                this.scheduleWeekMap.set(employee.EmployeeNo, employee)
            }

            let flexSchedulesDateData: any = dateData.flexSchedules;
            for (let sche of flexSchedulesDateData) {
                if (this.scheduleWeekMap.get(sche.EmployeeNo) && !this.scheduleWeekMap.get(sche.EmployeeNo).sche) {
                    this.scheduleWeekMap.get(sche.EmployeeNo).sche = [sche]
                    this.checkStatus(sche)
                } else if (this.scheduleWeekMap.get(sche.EmployeeNo)) {
                    this.scheduleWeekMap.get(sche.EmployeeNo).sche.push(sche)
                    this.checkStatus(sche)
                }
            }

            let newWeek = {
                weekStart: weekStart,
                weekEnd: weekEnd,
                scheduleWeek: this.scheduleWeekMap
            }

            this.$store.state.FlexSchedule.calendarFocusDate = this.dayFocus;

            this.getNewWeek(newWeek);

            for (let leave of leaves) {
                for (let leaveUnit of leave.leaveUnits) {
                    let dateDiff = moment(leaveUnit.leaveDate).diff(weekStart, 'days')

                    leaveUnit.calcUnit = leave.calcUnit;
                    leaveUnit.leaveCode = leave.leaveCode;
                    leaveUnit.leaveName = leave.leaveName;
                    this.scheduleWeekMap.get(leave.employeeNo).sche[dateDiff].leaveUnit = leaveUnit
                }
            }

            for (let unAva of dateData.unAvailabilities) {
                let weeklyDay = moment(unAva.unavailabilityDate).day()
                this.scheduleWeekMap.get(unAva.employeeNo).sche[weeklyDay].unAvailability = unAva
            }
        },

        checkStatus(sche: any) {
            if (sche.PublishStatus === WeeklyPublishStatus.draft && this.weekStatus === WeeklyPublishStatus.empty) {
                this.weekStatus = WeeklyPublishStatus.draft
            } else if (sche.PublishStatus === WeeklyPublishStatus.published) {
                this.weekStatus = WeeklyPublishStatus.published
            }
        },

        async chooseCalendarDate($event: any) {
            // console.log('event', $event)
            let element = $event.target.getClientRects()[0]
            this.datePickerTop = element.bottom;
            this.datePickerLeft = element.left;
            let datePickerResult;
            try {
                datePickerResult = await (this.$refs.DatePickerRef as any).toggleShow(this.dayFocus);
            } catch (e) {
                return
            }
            this.selectDate(datePickerResult)
        },

        async selectDate(date: Moment) {
            this.dayFocus = date;
            this.$store.state.FlexSchedule.calendarFocusDate = date
            let weekStart = date.clone().startOf('week')
            let weekEnd = date.clone().endOf('week')
            // return this.getDateData(weekStart, weekEnd)
            // this.getNewWeek(date)
            await this.loadDate(weekStart, weekEnd)

        },
        getSelectedWeek(selectedDate: any) {
            var weekStart = selectedDate.clone().startOf('week');
            var weekEnd = selectedDate.clone().endOf('week');

            this.weekDays = [];

            for (let i = 0; i <= 6; i++) {
                let momentDay = moment(weekStart).add(i, 'days')
                this.weekDays.push({
                    momentDay: momentDay,
                    day: momentDay.format("ddd, MMM Do")
                })
            }
        },
        async getDateData(startDate?: Moment, endDate?: Moment): Promise<FlexScheduleRequest> {
            let param = {}
            if (startDate && endDate) {
                param = {
                    employeeNo: Affinity.user.EmployeeNo,
                    startDate: startDate.format('YYYY-MM-DD'),
                    endDate: endDate.format('YYYY-MM-DD'),
                    forManagedEmployees: true,
                }
            }

            return Remote.GetFlexSchedules(param).then((res: any) => {
                // console.log('res', res)
                return this.reformatData(res)

            })
        },
        scheDaysToColumn() {
            // let scheduleWeekList = getScheduleWeek(12)

            for (let scheduleArr of this.scheduleWeek.values()) {
                let employeeName = scheduleArr.EmployeeName
                let employeeNo = scheduleArr.EmployeeNo
                for (let i = 0; i <= 6; i++) {

                    let findResult

                    if (scheduleArr.sche) {
                        findResult = scheduleArr.sche.find((day: any) => {
                            return day.dayInWeek === i;
                        })
                    } else {
                        scheduleArr.sche = []
                    }

                    if (!findResult) {
                        // if first day in array and that employee do not have flex schedule ([0].dayInWeek == undefined)
                        if (i === 0 && (!scheduleArr.sche[0]
                                        || (scheduleArr.sche[0].dayInWeek === null
                                            || scheduleArr.sche[0].dayInWeek === undefined ))) {
                            scheduleArr.sche[0] = {}
                            scheduleArr.sche[0].dayInWeek = 0;
                            scheduleArr.sche[0].employeeName = employeeName;
                            scheduleArr.sche[0].employeeNo = employeeNo;
                        } else {
                            scheduleArr.sche.push({
                                dayInWeek: i,
                                employeeName: employeeName,
                                employeeNo: employeeNo
                            })
                        }
                    }
                }

                scheduleArr.sche.sort((a: any, b: any) => a.dayInWeek - b.dayInWeek)
                for (let day of scheduleArr.sche) {
                    day.column = `grid-column-start: ${day.dayInWeek + 4}`;
                    day.editing = false;
                }
            }
        },
        getLastWeek() {
            this.dayFocus.subtract(7, 'days')

            let weekStart = this.dayFocus.clone().startOf('week')
            let weekEnd = this.dayFocus.clone().endOf('week')

            this.loadDate(weekStart, weekEnd)

            // this.getNewWeek()
        },
        getNextWeek() {
            this.dayFocus.add(7, 'days')
            let weekStart = this.dayFocus.clone().startOf('week')
            let weekEnd = this.dayFocus.clone().endOf('week')
            this.loadDate(weekStart, weekEnd)
            // this.getNewWeek()
        },

        getNewWeek(newWeek?: any) {
            let newWeekData = newWeek;
            this.getSelectedWeek(newWeekData!.weekStart)
            this.scheduleWeek = this.$store.state.FlexSchedule.scheduleWeek = newWeekData!.scheduleWeek
            this.scheDaysToColumn();
        },
        moreDetail(day: ScheduleDay) {
            this.$store.commit(FlexScheduleMutation.setScheduleDay, day)
        },

        getTotalHour(sche: ScheduleDay[]) {
            let totalHours = 0;
            for (let day of sche) {
                if (day.hours) {
                    totalHours += day.hours
                }
            }
            return totalHours.toFixed(2)
        },

        confirmEdit(day: any) {
            day.editing = false;
        },

        cancelEdit(day: any) {

        },

        editScheDay(day: any) {
            day.editing = true
        },

        reformatData(res: FlexScheduleRequestCap): FlexScheduleRequest {
            let flexScheduleRemoteData = res.FlexSchedules
            flexScheduleRemoteData.forEach((obj: any) => {
                obj.id = obj.Id;
                obj.startTime = moment(obj.StartTime).format('HH:mm')
                obj.endTime = moment(obj.EndTime).format('HH:mm')
                obj.hours = obj.Hours
                obj.scheduleDetail = obj.ScheduleData
                obj.scheduleDetail.breakLength = obj.scheduleDetail.BreakLength;
                obj.scheduleDetail.position = obj.scheduleDetail.Position;
                obj.scheduleDetail.positionTitle = obj.scheduleDetail.PositionTitle;
                obj.scheduleDetail.shiftId = obj.scheduleDetail.ShiftId;
                obj.scheduleDetail.costElement1 = obj.scheduleDetail.CostElement1;
                obj.scheduleDetail.costElement2 = obj.scheduleDetail.CostElement2;
                obj.dayInWeek = moment(obj.Date).day()
            })

            let unAvailabilitiesData = res.Unavailabilities.map((obj: any) => {
                return {
                    id: obj.EmpAvailability.Id,
                    unavailabilityDate: obj.UnavailabilityDate,
                    employeeNo: obj.EmpAvailability.EmployeeNo,
                    startTime: obj.EmpAvailability.StartTime,
                    endTime: obj.EmpAvailability.EndTime,
                    reason: obj.EmpAvailability.Reason
                }
            })

            let flexScheduleRequest = {
                flexSchedules: flexScheduleRemoteData,
                unAvailabilities: unAvailabilitiesData
            }
            return flexScheduleRequest;
        },

        publishCurrentWeek() {
            let groupedSche: any[] = [];

            for (const [key, employeeData] of this.scheduleWeekMap.entries()) {
                let scheWithData = employeeData.sche.filter((sche: any) => {
                    return !!sche.Id
                })

                groupedSche = groupedSche.concat(scheWithData)
            }

            let data = groupedSche.map(sche => {
                return {
                    Id: sche.id,
                    PublishStatus: "published"
                }
            })

            Dialog.confirmOrCancel({
                message: `Are you sure you want to publish the schedule for week ${this.dayFocus.week()} ?`,
                confirmButtonText: 'Yes'
            }).then(() => {
                Dialog.loading()
                Remote.PatchFlexSchedule(data)
                .then(() => {
                    this.reloadAfterEdit()
                    Dialog.clear()
                }, (e) => {
                    console.error(e);
                    Dialog.alert('Something went wrong.')
                })
            })
        },

        changeDisplayControl() {
            this.showControl = !this.showControl
        }

    }
})
</script>

<style scoped lang="scss">

$aff-blue: #00AEEF;
$aff-dark-blue: #1268B3;
$border-color: #cbcbcb;
.schedule-head-grid {
    display: grid;
    width: 100%;
    //grid-template-columns: 12% 12% 12% 12% 12% 12% 12% 12%;
    grid-template-columns: 8% 90px 90px repeat(7, 1fr);
    grid-gap: 1px;
    height: 23px;
    background: $border-color;
}

.weekDetail {
    font-size: 16px;
    .arrow-height {
        height: 30px;
        margin-top: 1px;
        cursor: pointer;
    }

}

.table-body {
    display: grid;
    //grid-template-rows: repeat(auto-fill, 60px);
    //grid-template-rows: min-content;
    grid-gap: 1px;
}

.table-cell {
    padding: 3px 5px 3px 5px;
    //background-color: white;
    background: linear-gradient(to bottom, #fcfcfc 0%, #efefef 49%, #e2e2e2 51%, #d6d6d6 100%);
    //border: 1px solid #cbcbcb
    text-align: center;
}

.schedules-arr {
    display: grid;
    width: 100%;
    grid-template-columns: 8% 90px 90px repeat(7, 1fr);
    //grid-template-rows: min-content;
    //grid-template-rows: repeat(auto-fit, 60px);
    grid-gap: 1px;

}

.flex-sche-table {
    border: 1px solid $border-color;
    font-size: 14px;
    border-collapse: collapse;
    background: $border-color;
}

.content-cell {
    display: grid;
    justify-items: center;
    align-items: center;
    background: white;
    text-align: center;
    min-height: 60px;
}


.table-head {
    //background: linear-gradient(to bottom, #fcfcfc 0%, #efefef 49%, #e2e2e2 51%, #d6d6d6 100%);
    //padding: 3px 5px 3px 5px;
    height: 23px;
}

.status-section {
    position: relative;
}

.show-control-options {
    position: absolute;
    display: flex;
    flex-direction: column;
    padding: 5px;
    margin-top: 5px;
    background: white;
    z-index: 1;

}

.inline-block-middle {
    display: inline-block;
    vertical-align: middle;
}

.action-buttons {
    display: flex;
    flex-direction: column;
    padding: 5px 10px;
}

.content-employee-name {
    cursor: pointer;

    &:hover {
        background: $aff-blue;
        color: white;
    }
}
</style>