<template>
    <!--    <div v-model="dialog"  content-class="dialog-width">-->
    <modal ref="alertModalRef" :noBox="true">
        <template v-slot:body>
            <div class="promt-box">
                <div>
                    <div class="mr-2 d-inline" v-if="showLoader">
                        <img class="loader-icon"
                             src="data:image/gif;base64,">
                    </div>

                    <div class="content " v-html="message"></div>
                </div>

                <div class="promptButtons mt-4" v-if="showButtons">
                    <button type="button" class="orange white--text small" v-bind:class="{'mr-4' : showCancel}"  tabindex="9999998"
                            @click="onOk">
                        <!--                    <v-icon small>icon-tick</v-icon>-->
                        <span class="label text-capitalize">{{ confirmButtonText }}</span>
                    </button>
                    <span class="">&nbsp;</span>
                    <button class="grey white--text small" v-if="showCancel" small tabindex="9999999"
                            @click="onCancel">
                        <!--                    <v-icon class="mr-1" small>icon-cancel</v-icon>-->
                        <span class="label text-capitalize">{{ cancelButtonText }}</span>
                    </button>
                </div>
            </div>
        </template>

        <!--        <div v-if="showLoader">-->
        <!--            <img src="data:image/gif;base64,">-->
        <!--            <div class="content" v-html="message" ></div>-->
        <!--        </div>-->
    </modal>
</template>

<script lang="ts">
import {emitter} from '../main'
import Modal from "@/components/common/Modal.vue";
import {defineComponent} from "vue";

export default defineComponent({
    name: "alert",
    components: {
      Modal
    },
    data() {
        return {
            dialog: false,
            message: 'some message ',
            showLoader: false,
            showButtons: false,
            showCancel: false,
            confirmButtonText: 'Ok',
            cancelButtonText: 'Cancel',
        }
    },
    mounted() {

        // console.log('mounted in alert')
        emitter.on('alert', (message) => {
            console.log('emitter alert')
            this.dialog = true;
            this.message = message;
            this.showLoader = false;
            this.showButtons = true;
        })

        emitter.on('dialog--loading--request', (message) => {
            console.log('emitter loading request')
            this.dialog = true;
            this.message = message;
            this.showCancel = false;
            this.showButtons = false;
            this.showLoader = true;
        })

        emitter.on('clearDialog', () => {
            console.log('emitter clear dialog')
            this.clearDialog();
            this.dialog = false;
        });
        emitter.on('dialog', (inputObj) => {
            console.log('emitter on dialog')
            this.toggleShow();
            this.dialog = !!inputObj;
            this.message = inputObj.message;
            this.showCancel = inputObj.showCancel;
            this.showButtons = inputObj.showButtons;
            this.showLoader = inputObj.showLoader;
            this.confirmButtonText = inputObj.confirmButtonText ? inputObj.confirmButtonText : 'Ok'
            this.cancelButtonText = inputObj.cancelButtonText ? inputObj.cancelButtonText : 'Cancel'

            if (inputObj.onOk) {
                this.onOk = () => {
                    inputObj.onOk();
                    this.dialog = false;
                    this.toggleShow()
                }
            }

            if (inputObj.onCancel) {
                this.onCancel = () => {
                    inputObj.onCancel();
                    this.dialog = false;
                    this.toggleShow()
                }
            }

        })
    },
    methods: {
        // onOk() {
        //     this.dialog = false;
        // },
        // onCancel() {
        //     this.dialog = false;
        // },
        clearDialog() {
            (this.$refs.alertModalRef as any).toggleShow(false);
        },
        toggleShow() {
            (this.$refs.alertModalRef as any).toggleShow();
        },
        onOk() {
            (this.$refs.alertModalRef as any).toggleShow();

        },
        onCancel() {
            (this.$refs.alertModalRef as any).toggleShow();

        },
    }
})
</script>

<style scoped>

::v-deep .dialog-width {
    width: auto;
    max-width: 800px;
}

.promt-box {
    padding: 20px 60px;
    font-size: 16px;
    background: #333;
    color: #fff;
    text-align: center;
    line-height: 1.2;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    -webkit-box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
    -moz-box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
    box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
    z-index: 10000000;
}


.content {
    display: inline-block;
    vertical-align: middle;
    text-align: center;
}

.loader-icon {
    vertical-align: middle;
}
</style>