
import {defineComponent, onMounted, ref} from 'vue'
import EditAvailability from "@/components/Availability/EditAvailability.vue";
import Remote from '@/remote/scheduleRequest'
import moment from "moment";
import AvailabilityLookup from "@/components/Availability/AvailabilityLookUp";
import Dialog from "@/components/dialog";
declare var Affinity: any

export default defineComponent({
    components: {EditAvailability},
    data() {
        return {
            // girdData: [{
            //         dayOfWeek: 'Monday',
            //         startTime: '9:00',
            //         endTime: '12:00',
            //         recurrence: 'Every week',
            //         startDay: '12/02/2021',
            //         endDay: '02/05/2021',
            //         reason: 'UniversityClasses'
            //     }, {
            //         dayOfWeek: 'Monday',
            //         startTime: '9:00',
            //         endTime: '12:00',
            //         recurrence: 'Every week',
            //         startDay: '12/02/2021',
            //         endDay: '02/05/2021',
            //         reason: 'UniversityClasses'
            //     }
            // ],

        }
    },

    setup () {
        const isEditingDialog = ref(false);
        const EditAvailabilityRef = ref<InstanceType<typeof EditAvailability>>();
        const girdData = ref([]);
        const selectedAva = ref();

        function openEditDialog(isEdit: boolean, selectedData?: any) {
            isEditingDialog.value = isEdit;
            EditAvailabilityRef.value!.openEditDialog(selectedData);
        }

        function loadAvailability() {
            Remote.GetAvailabilities(Affinity.user.EmployeeNo).then((res) => {
                let availabilities = res.map((availability: any) => {
                    return Object.fromEntries(
                        Object.entries(availability).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                    );
                })

                girdData.value = availabilities.map((availabilities: any) => {
                    availabilities.startDate = moment(availabilities.startDate).format('DD/MM/YYYY')
                    if (availabilities.endDate) {
                        availabilities.endDate = moment(availabilities.endDate).format('DD/MM/YYYY')
                    }
                    availabilities.dayOfWeek = AvailabilityLookup.findFullDaysByShort(availabilities.dayOfWeek)

                    return availabilities
                })
            })
        }

        onMounted(() => {
            loadAvailability();
        })

        // openEditDialog()
        return { girdData, openEditDialog, selectedAva, loadAvailability,
            isEditingDialog, EditAvailabilityRef }
    },

    mounted() {

    },

    methods: {
        confirmDelete(data: any) {
            Dialog.confirmOrCancel({
                message: 'Are you sure you want to delete this Availability?'
            }).then(() => {
                this.deleteAva(data.id)
            })
        },

        deleteAva(id: number) {
            Remote.DeleteAvailabilities(id)
                .then(res => {
                   this.loadAvailability()
                }, err => {
                    Dialog.alert('Something went wrong. Delete Availability failed')
                })

        },
    }
})
