<template>
    <Modal ref="ModalRef">
        <template v-slot:header>
            <div>
                <h2>
                    <b>Create Draft Schedule</b>
                </h2>
            </div>
        </template>
        <template v-slot:body>
            <div>
                <div class="line-break my-4"></div>
                <form>
                    <div>
                        <div class="d-flex align-center">
                            <input type="radio" name="source" value="import" class="mt-0 ml-0" v-model="sourceMode">
                            <span v-tooltip.top="{content: 'Copies information from an employees personal work schedule in Affinity and pastes it into a current or future flexi schedule.'}">
                                Copy from Affinity
                            </span>
                        </div>
                        <div class="mt-2 draft-option pa-2">
                            <div class="grey-wrapper" v-if="sourceMode !== 'import'"></div>
                            <div>
                                <span class="input-label"
                                      v-tooltip.top="{content: 'The date in the flexi schedule when copied information will start. </br> For example, if you copy a 5-day block from 1 April and start it on 11 July, it will run from 11- 15 July.'}">Start On</span>
                                <input class="input-box" type="text" v-model="scheduleStartDateText"
                                       @click="updateDateTime($event, 'ScheduleStartDate')">
                            </div>
                            <div class="mt-2">
                                <span class="input-label"
                                      v-tooltip.top="{content: 'The number of days to copy.'}">Number of Days</span>
                                <input class="input-box" type="text" v-model="copyPersonalDuration">
                            </div>
                        </div>

                    </div>
                    <div class="line-break my-4"></div>
                    <div>
                        <div class="d-flex align-center">
                            <input type="radio" value="copy" name="source" class="mt-0 ml-0" v-model="sourceMode">
                            <span v-tooltip.top="{content: 'Copies information from a previous flexi schedule and pastes it into a current or future flexi schedule.'}">Copy From Flexi Schedule</span>
                        </div>
                        <div class="mt-2 draft-option pa-2">
                            <div class="grey-wrapper" v-if="sourceMode !== 'copy'"></div>
                            <div>
                                <span class="input-label"
                                      v-tooltip.top="{content: 'The date to copy from.'}">From Date</span>
                                <input class="input-box" type="text" v-model="copyFromDateText"
                                       @click="updateDateTime($event, 'copyFromDate')">
                            </div>
                            <div class="mt-2">
                                <span class="input-label"
                                      v-tooltip.top="{content: 'The number of days to copy.'}">Number of Days</span>
                                <input class="input-box" type="text" v-model.number="copyToDateDuration">
                            </div>
                            <div class="mt-2">
                                <span class="input-label"
                                      v-tooltip.top="{content: 'The date in the flexi schedule when copied information will start. </br> For example, if you copy a 5-day block from 1 April and start it on 11 July, it will run from 11- 15 July.'}">Start On</span>
                                <input class="input-box" type="text" v-model="targetDateText"
                                       @click="updateDateTime($event, 'targetDate')">
                            </div>
                        </div>

                    </div>
                </form>

                <div class="line-break my-4"></div>
                <div>
                    <div>
                        Paste Options
                    </div>
                    <div class="mt-4 d-flex align-center">
                        <input type="radio" name="copyOption" value="replace" class="mt-0 ml-0" v-model="copyOption">
                        <span v-tooltip.top='{content: "When copied information is pasted in, it will replace all information on the days specified."}'>Replace All</span>
                    </div>
                    <div class="d-flex align-center mt-2">
                        <input type="radio" name="copyOption" value="merge" class="mt-0 ml-0" v-model="copyOption">
                        <span v-tooltip.top='{content: "When copied information is pasted in, it will only replace information on the days specified if there is a conflict."}'>Merge</span>
                    </div>
                </div>
                <date-picker ref="DatePickerRef" :top="datePickerTop" :left="datePickerLeft"></date-picker>

            </div>

        </template>
        <template v-slot:footer>
            <div class="d-flex justify-end full-width mt-4">
                <button class="blue xsmall white--text mr-2" @click="actionByMode">Create</button>
                <button class="orange xsmall white--text" @click="cancel">Cancel</button>
            </div>
        </template>

    </Modal>
</template>

<script lang="ts">
import {defineComponent} from "vue";
import Modal from "@/components/common/Modal.vue";
import moment, {Moment} from "moment";
import DatePicker from "@/components/common/DatePicker.vue";
import Remote from '@/remote/scheduleRequest'
import Dialog from "@/components/dialog";

export enum CopyOptions {
    copy = 'replace',
    merge = 'merge'
}

export default defineComponent({
    name: "DraftCreationDialog",
    components: {
        Modal,
        DatePicker
    },
    data() {
        return {
            datePickerTop: 0,
            datePickerLeft: 0,
            sourceMode: 'copy',
            copyOption: 'replace',

            //
            scheduleStartDateText: '',
            copyFromDateText: '',
            targetDateText: '',

            //
            scheduleStartDate: moment(),
            copyFromDate: moment(),
            copyToDateDuration: 0,
            targetDate: moment(),

            //
            dateTarget: '',
            copyPersonalDuration: 0
        }
    },
    mounted() {
        // this.toggleShow();
    },
    methods: {
        resetData() {
                this.datePickerTop = 0;
                this.datePickerLeft = 0;
                this.sourceMode = 'copy';
                this.copyOption = 'replace';
                this.scheduleStartDateText = '';
                this.copyFromDateText = '';
                this.targetDateText = '';
                this.scheduleStartDate = moment();
                this.copyFromDate = moment();
                this.copyToDateDuration = 0;
                this.targetDate = moment();
                this.dateTarget = '';
                this.copyPersonalDuration = 0;
        },
        toggleShow() {
            this.resetData();
            (this.$refs.ModalRef as any).toggleShow();
        },

        actionByMode() {
            if (this.sourceMode === 'copy') {
                this.createDraft();
            } else {
                this.importFromPersonal();
            }
        },

        importFromPersonal() {
            Dialog.loading()
            let data = {
                CopyFromPersonalSchedule: true,
                EmployeeNumbers:  this.$store.state.EmployeeModule.employees.map((employee: any) => employee.employeeNo),
                PersonalSchedCopyDurationInDays: this.copyPersonalDuration,
                TargetDate: this.scheduleStartDate.format('DD/MMM/YYYY'),
                CopyOption: this.copyOption
            }

            Remote.CopyFlexSchedule(data)
            .then(res => {
                (this.$refs.ModalRef as any).toggleShow();
                this.$emit('reload');
            })
            .finally(() => {
                // Dialog.clear();
            })
        },

        createDraft() {
            Dialog.loading()
            let data = {
                CopyFromPersonalSchedule: false,
                EmployeeNumbers: this.$store.state.EmployeeModule.employees.map((employee: any) => employee.employeeNo),

                CopyFromDate: this.copyFromDate.format('DD/MMM/YYYY'),
                CopyToDate: this.copyFromDate.add(this.copyToDateDuration - 1, 'days').format('DD/MMM/YYYY'),
                TargetDate: this.targetDate.format('DD/MMM/YYYY'),
                CopyOption: this.copyOption
            }
            Remote.CopyFlexSchedule(data)
            .then(res => {
                (this.$refs.ModalRef as any).toggleShow();
                this.$emit('reload');
            })
            .finally(() => {
                // Dialog.clear();
            })
        },

        async updateDateTime($event: any, selectedDateTarget: string) {
            let element = $event.target.getClientRects()[0]
            this.datePickerTop = element.bottom;
            this.datePickerLeft = element.left;

            try {
                switch (selectedDateTarget) {
                    case 'ScheduleStartDate' :
                        this.scheduleStartDate = await (this.$refs.DatePickerRef as any).toggleShow(this.scheduleStartDate);
                        this.scheduleStartDateText = this.scheduleStartDate.format('DD/MM/YYYY')
                        return;
                    case 'copyFromDate' :
                        this.copyFromDate = await (this.$refs.DatePickerRef as any).toggleShow(this.copyFromDate);
                        this.copyFromDateText = this.copyFromDate.format('DD/MM/YYYY')
                        return;
                    case 'targetDate' :
                        this.targetDate = await (this.$refs.DatePickerRef as any).toggleShow(this.targetDate);
                        this.targetDateText = this.targetDate.format('DD/MM/YYYY')
                        return;

                }
            } catch (e) {
                return
            }
        },
        cancel() {
            this.toggleShow();
        }

    }
})
</script>

<style scoped lang="scss">
//@use '../../style/colors';
    .line-break {
        border-top: 1px solid rgba(#7e7e7e, 0.5);
    }

    .input-label {
        display: inline-block;
        width: 140px;
    }

    .input-box {
        width: 100px;
        padding: 4px 12px;
        border: 1px solid #767676;
        font-size: 12px;
        border-radius: 4px;
    }

    .grey-wrapper {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background: rgba(0,0,0,.12);
        border-radius: 5px;
        z-index: 1
    }

    .draft-option {
        position: relative;
    }


</style>