<template>
    <div class="outter-h-scroll">
        <div class="fix-content-width">
            <search-employee class="mr-4 mr-xs-0" ref="SearchEmployeeRef"
                             @updateSelectedEmpList="updateEmpList"></search-employee>
            <div class="emp-detail pa-4 mr-4 ml-4 mr-xs-0" v-if="bulkEmpList.length > 0">
                <div class="section">
                    <div class="d-flex justify-space-between">
                        <h3>Selected Employees</h3>
                    </div>

                    <div class="emp-selection-list mt-4 caption">
                        <div v-for="(emp, index) in bulkEmpList"
                             class="d-flex justify-space-between align-center py-1 px-2"
                             v-bind:class="{'emp-select-item-odd': index % 2}">
                            <div>
                                <span class="list-text"
                                      v-tooltip.right="{content: 'Remove from Employee Selection List'}">{{ emp.EmployeeName }} ({{ emp.EmployeeNo }})</span>
                            </div>
                            <div>
                                <button v-if="!isMobileView"
                                        class="blue small"
                                        v-tooltip.right="{ content: 'View Schedule Allocations'}"
                                        style="margin-right:3px;"
                                        @click="showScheduleDetail(emp)">
                                    <span class="icon-calendar white lh-0"></span>
                                </button>

                                <button v-if="!isMobileView"
                                        class="orange small"
                                        v-tooltip.right="{ content: 'Remove from Employee Selection List'}"
                                        @click="removeEmployee(emp)">
                                    <span class="icon-minus white lh-0"></span>
                                </button>

                                <button v-if="isMobileView"
                                        class="orange small"
                                        @click="removeEmployee(emp)">
                                    <span class="icon-minus white lh-0"></span>
                                </button>

                                <button v-if="isMobileView" class="blue ml-1 small"
                                        style="margin-right:3px;" @click="showScheduleDetail(emp)">
                                    <span class="icon-calendar white lh-0"></span>
                                </button>
                            </div>
                        </div>
                    </div>
                    <div class="schedules-buttons mt-2 d-flex justify-end">
                        <button v-if="!isMobileView" class="button grey small"
                                @click="clearSelectedEmployee()"
                                v-tooltip.right="{ content: 'Clear Employee Selection List'}">
                            <span class="icon-cancel mr-2 vert-mid white lh-0"></span><span class="text-capitalize white ">Clear</span>
                        </button>

                        <button v-if="isMobileView" class="button grey small"
                                @click="clearSelectedEmployee()">
                            <span class="icon-cancel vert-mid white lh-0"></span><span class="text-capitalize white">Clear</span>
                        </button>
                    </div>
                </div>
                <div v-on:submit.prevent="submitForm">
                    <div class="section mt-4" style="height:100%;">
                        <h3 class="bold-text">Allocate Schedule</h3>
                        <div class="mt-4 allocate-sche-form">
                            <form>
                                <div class="custom-select" tabindex="1" ref="selectRef">
                                    <div class="custom-select-selected">
                                        <input class="custom-select-input" ref="selectInputRef"
                                               v-model="selectSearchInput" @keyup="updateSearchList">
                                    </div>
                                    <div class="custom-select-items">
                                        <div class="custom-select-item" ref="selectDropdownRef"
                                             v-for="(option, index) in cloneSchedList" :key="index"
                                             :tabindex="index+99"
                                             @keyup.down="navDownList"
                                             @click="chooseSchedule($event, option)">
                                            <div class="cell-config">
                                                {{ option.Name }}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div v-if="selectedSchedule && selectedSchedule.Name" class="mt-4">
                                    <div class="bold-text">Description</div>
                                    <div class="caption">{{ selectedSchedule.Description }}</div>
                                    <div class="bold-text mt-4">No. of Weeks: <span
                                        class="ml-4">{{ selectedSchedule.NoOfWeeks }}</span>
                                    </div>
                                </div>
                                <div class="d-flex justify-space-between">
                                    <div class="mt-4 date-input-warpper">
                                        <div class="bold-text">Date From</div>

                                        <input :value="$filters.reformatDate(schedAllocation.DateFrom)"
                                              class="d-inline-flex ma-0 pa-0 mr-2 caption date-input-width date-input"
                                              @change="updateDateText($event, schedAllocation.DateFrom)"
                                              @click="updateDateTime($event, schedAllocation.DateFrom, 'DateFrom')"
                                              @click:clear="fromDate = false">
                                    </div>
                                    <div class="mt-4 date-input-warpper">
                                        <div class="bold-text">Date To</div>
                                        <input :value="$filters.reformatDate(schedAllocation.DateTo)"
                                              class="d-inline-flex ma-0 pa-0 caption date-input-width date-input"
                                              @change="updateDateText($event, schedAllocation.DateTo)"
                                               @click="updateDateTime($event, schedAllocation.DateTo, 'DateTo')"
                                               @click:clear="toDateMenu = false">
                                    </div>
                                </div>

                                <div class="mt-4">
                                    <div class="bold-text">Notes</div>
                                    <textarea rows="3" class="caption"
                                              @keyup="hasChanges = true"
                                              v-model="schedAllocation.Comment"> </textarea>
                                </div>
                                <div class="form-row mt-4" v-if="schedAllocation.DateFrom">
                                    <label class="label-width">End existing schedules on {{ capDate }}</label>
                                    <input type="checkbox" value="1" v-model="schedAllocation.EndExistingScheds">
                                </div>
                                <div class="schedules-buttons mt-4 d-flex justify-end">
                                    <button class="grey vert-mid small" @click="closeSchedAllocation()">
                                        <span class="icon-cancel vert-mid lh-0 white--text mr-1"></span>
                                        <span class="text-capitalize white--text">Cancel</span>
                                    </button>
                                    <button class="green ml-4 small" @click="allocateEmployees()">
                                        <span class="icon-paperplane vert-mid lh-0 white--text mr-1"></span>
                                        <span class="text-capitalize white--text">Allocate</span>
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <template v-if="selectedEmpForDetail">
                <employee-schedule ref="EmployeeScheduleRef" :employee="selectedEmpForDetail"
                                   @close="resetSelectedEmpForDetail"></employee-schedule>
            </template>
            <date-picker ref="DatePickerRef" :top="dateModalTop" :left="dateModalLeft"></date-picker>

        </div>
    </div>

</template>
<script lang="ts">
import Remote from "@/remote/scheduleRequest";
import {emitter} from "../../main";
import moment, {Moment} from "moment";
import EmployeeSchedule from "./EmployeeSchedule.vue";
import SearchEmployee from "./SearchEmployee.vue";
import Dialog from '@/components/dialog';
import {defineComponent} from "vue";
import DatePicker from "@/components/common/DatePicker.vue";

export default defineComponent({
    name: 'employeeAllocation',
    components: {
        SearchEmployee,
        EmployeeSchedule,
        DatePicker,
    },
    data: function () {
        return {
            bulkEmpList: <any[]>[],
            schedList: <any[]>[],
            hasChanges: false,

            // select schedule
            cloneSchedList: <any[]>[],
            selectSearchInput: '',

            // form model
            schedAllocation: {
                WorkschedId: -1,
                DateFrom: <Moment | null>moment(),
                DateTo: <Moment | null>null,
                Comment: '',
                EndExistingScheds: true
            },

            selectedSchedule: <any>{},

            // employee-chedule component input data
            selectedEmpForDetail: null,

            // datePicker
            startDateMenu: false,
            toDateMenu: false,
            startDatePickerValue: <any>'',
            toDatePickerValue: <any>'',
            datePickerTargetDay: <any>null,
            dateType: '',

            //
            isMobileView: false,

            //date modal
            dateModalTop: null,
            dateModalLeft: null,
        };
    },

    // created: function () {
    //
    // },
    mounted: function () {
        this.isMobileView = this.checkIsMobileView()
        // Dialog.loading()
        Remote.StandardSchedules().then(response => {
            this.schedList = response.Data.filter((data: any) => data.Archive === false && data.ScheduleType === 0);
            this.cloneSchedList = this.schedList.slice();
            //  Dialog.clear()
        })
        this.$watch('schedAllocation', (newVal: any, oldVal: any) => {
            if (newVal === oldVal) {
                this.hasChanges = true; //ignore employee list changes.
            }
        }, {deep: true});
    },
    computed: {
        selectedSched() {
            if (this.schedAllocation.WorkschedId === -1) {
                return false;
            }

            // todo: cant not use this.schedList in return
            let schedList: any = this.schedList;
            return schedList.find((sched: any)=> {
                return sched.Id === this.schedAllocation.WorkschedId
            })
        },

        capDate: function () {
            if (!this.schedAllocation.DateFrom) {
                return
            }

            let schedAllocation: any = this.schedAllocation

            let date = moment(schedAllocation.DateFrom);
            let yesterday = date.subtract(1, 'day')
            return yesterday.format('DD/MM/YYYY');
        },
    },

    methods: {
        // todo: fix calendar
        // getDay(date) {
        //     const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
        //
        //     let i = new Date(date).getDay(date)
        //     return daysOfWeek[i]
        // },

        navDownList() {
            console.log('nav down list')
        },

        updateSearchList(event: any) {
            if (event.key === "ArrowDown") {
                (this.$refs.selectDropdownRef as any).focus();
            }
            this.cloneSchedList = this.schedList.filter(o => {
                return o.Name.toLowerCase().indexOf(this.selectSearchInput.toLowerCase()) > -1
            })
        },

        updateDateText(event: any, target: any) {
            this.startDateMenu = false
            this.toDateMenu = false;
            target.set(moment(event, 'DD/MM/YYYY').toObject())
            // todo: add check for is correct format
            this.datePickerFormattedFromDate();
            this.datePickerFormattedToDate();
        },

        datePickerFormattedFromDate() {
            this.startDatePickerValue = this.schedAllocation.DateFrom ? moment(this.schedAllocation.DateFrom).format('YYYY-MM-DD') : undefined; // Date picker objects needs date in this particular format
        },
        datePickerFormattedToDate() {
            this.toDatePickerValue = this.schedAllocation.DateTo ? moment(this.schedAllocation.DateTo).format('YYYY-MM-DD') : undefined; // Date picker objects needs date in this particular format
        },

        updateEmpList(empList: any) {
            this.bulkEmpList = empList
        },
        startDateDatePickerHandler(date: any) {
            if (typeof date === 'string')
                date = moment(date);
            this.schedAllocation.DateFrom = date;
            this.datePickerFormattedFromDate()
        },
        toDateDatePickerHandler(date: any) {
            if (typeof date === 'string')
                date = moment(date);
            this.schedAllocation.DateTo = date;
            this.datePickerFormattedToDate()
        },

        removeEmployee: function (emp: any) {
            (this.$refs.SearchEmployeeRef as any).removeEmployee(emp)
        },

        clearSelectedEmployee: function () {
            if (this.hasChanges) {
                Dialog.checkChange({
                    message: 'There are currently Schedule Allocation changes <br /> Do you want to discard your changes?',
                    onOk: () => {
                        (this.$refs.SearchEmployeeRef as any).clearSelectedEmployeeList();
                        this.resetSchedAllocation()
                    }
                })
            } else {
                (this.$refs.SearchEmployeeRef as any).clearSelectedEmployeeList();
            }
        },

        allocateEmployees: function () {
            var warningMsgs = '';
            if (this.selectedSchedule.Id < 1 || !this.selectedSchedule.Id) {
                warningMsgs += '<br />Select a schedule that you want to allocate.';
            }

            if (this.schedAllocation.DateFrom && this.schedAllocation.DateTo &&
                this.schedAllocation.DateFrom.diff(this.schedAllocation.DateTo) > 0) {
                warningMsgs += '<br />Date To cannot be before Date From.';
            }

            if (warningMsgs != '') {
                Dialog.alert(warningMsgs)
                // emitter.emit('dialog', {
                //     message: warningMsgs,
                //     showButtons: true,
                //     noClose: false,
                //     showLoader: false
                // });

                return;
            }

            var postData = {
                WorkschedId: this.selectedSchedule.Id,
                EmployeeList: <any[]> [],
                DateFrom: moment(this.schedAllocation.DateFrom) ? moment(this.schedAllocation.DateFrom).format('DD/MMM/YYYY') : null,
                DateTo: moment(this.schedAllocation.DateTo) ? moment(this.schedAllocation.DateTo).format('DD/MMM/YYYY') : null,
                Comment: this.schedAllocation.Comment,
                EndExistingScheds: this.schedAllocation.EndExistingScheds
            };

            if (this.bulkEmpList.length > 0) {
                this.bulkEmpList.forEach((emp: any) => {
                    postData.EmployeeList.push(emp.EmployeeNo);
                });

            }

            // todo: did not find this.selectedEmp ??!!
            // else if (this.selectedEmp) {
            //     postData.EmployeeList = [this.selectedEmp.EmployeeNo];
            // }

            Dialog.loading()
            Remote.AllocateSchedule(postData).then(res => {
                this.hasChanges = false;
                this.resetSchedAllocation();
                Dialog.clear()
                if (this.$refs.EmployeeScheduleRef) {
                    (this.$refs.EmployeeScheduleRef as any).getEmpWorkScheds()
                }

                // this.getEmpWorkScheds
            })
                .catch(err => {
                    Dialog.alert(err.message)
                    console.error('err', err)
                })
        },

        closeSchedAllocation: function () {
            if (this.hasChanges) {
                Dialog.checkChange({
                    message: 'You have unsaved changes. Are you sure to discard the changes?',
                    onOk: () => {
                        this.resetSchedAllocation();
                        this.hasChanges = false;
                        (this.$refs.SearchEmployeeRef as any).clearSelectedEmployeeList();
                    },
                })
            } else {
                (this.$refs.SearchEmployeeRef as any).clearSelectedEmployeeList();
            }
        },

        showScheduleDetail: function (employee: any) {
            this.selectedEmpForDetail = employee;
        },

        resetSelectedEmpForDetail() {
            this.selectedEmpForDetail = null;
        },

        resetSchedAllocation: function () {
            this.hasChanges = false;
            this.schedAllocation = {
                WorkschedId: -1,
                DateFrom: moment(),
                DateTo: null,
                Comment: '',
                EndExistingScheds: true
            };
        },

        checkIsMobileView: function () {
            if (navigator.userAgent.match(/Android/i)
                || navigator.userAgent.match(/webOS/i)
                || navigator.userAgent.match(/iPhone/i)
                || navigator.userAgent.match(/iPad/i)
                || navigator.userAgent.match(/iPod/i)
                || navigator.userAgent.match(/BlackBerry/i)
                || navigator.userAgent.match(/Windows Phone/i)
            ) {
                return true;
            } else {
                return false;
            }
        },

        chooseSchedule($event: any, option: any) {
            $event.target.blur()
            this.selectedSchedule = option;
            this.selectSearchInput = this.selectedSchedule.Name;

            let a = $event.target.blur();

            let b = (this.$refs.selectRef as any);
            b.blur();
            let c = (this.$refs.selectInputRef as any);
            c.blur();
        },

        updateDateTime($event: any, dateFrom: any, dateType: string) {
            let element = $event.target.getClientRects()[0]
            this.dateModalTop = element.bottom;
            this.dateModalLeft = element.left;
            this.datePickerTargetDay = dateFrom;
            this.dateType = dateType;
            this.openDatePicker()
        },

        async openDatePicker() {
            // (this.$refs.DatePickerRef as any).toggleShow();
            let result = await ((this.$refs.DatePickerRef as any).toggleShow(this.datePickerTargetDay));
            if (this.dateType === 'DateTo') {
                this.schedAllocation.DateTo = result
            } else if (this.dateType === 'DateFrom') {
                this.schedAllocation.DateFrom = result
            }

        },

        submitForm() {
            //
        }
    },
})
</script>


<style scoped lang="scss">
@use '../../style/colors';

$button-blue: #44b5ec;
$table-odd-color: #f6f6f6;

.outter-h-scroll {
    overflow-x: auto;
}

.fix-content-width {
    width: max-content;

    @media only screen and (max-width: 980px) {
        width: auto;
        text-align: center;
    }
}


.vert-mid {
    vertical-align: middle;
}

.emp-detail {
    display: inline-block;
    width: 380px;
    background: #efefef;

    @media only screen and (max-width: 980px) {
        margin-top: 10px;
    }

    @media only screen and (max-width: 600px) {
        margin-top: 8px;
        width: 100%;
    }
}

.emp-selection-list {
    max-height: 150px;
    overflow: auto;
    background: white;
}

.emp-select-item-odd {
    background-color: $table-odd-color;
}

.aff-table table tr .emp-selected {
    background: $button-blue;
    color: white;
}

textarea {
    width: 100%;
    padding: 5px;

    margin: 0;
    border-top: 1px solid #b3b3b3;
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #fff;
    border-left: 1px solid #e6e6e6;
    font-size: 12px;
    font-family: 'Montserrat', Arial, 'Helvetica Neue', Helvetica, Sans-Serif;
    background: #fff;
}

.allocate-sche-form {
    font-size: 12px;
}

.date-input-width {
    max-width: 160px;
}

.date-input-width {
    width: 90%
}

.date-input-warpper {
    width: 45%;
}

.custom-select {
    font-size: 12px;
}

.custom-select-selected {
    min-height: 24px;
    padding: 4px 1.5em 4px 0.2em;
    border: none;
    border-radius: 0;
    background-color: transparent;
    border-bottom: 1px solid rgba(colors.$grey, 0.7);

}

.custom-select-selected:after {
    top: 8px;
    right: 0;
}

.custom-select:focus-within {
    .custom-select-selected {
        border: none;
        border-radius: 0;
        border-bottom: 1px solid colors.$aff-dark-blue;
    }

    .custom-select-selected:after {
        transform: rotate(180deg);
        top: 4px;
    }
}

.custom-select-items {
    background: white;
}

.custom-select-item {
    color: colors.$dark-blue-app;
    // padding-left: 1em;
    cursor: pointer;
    user-select: none;
    padding: 4px 1em;
}

.custom-select-item:hover {
    color: colors.$aff-dark-blue;
    background-color: lighten(colors.$aff-blue, 50%);
}

.custom-select-input {
    border: none;
    background: transparent;
}

.cell-config {
    pointer-events: none;
}
</style>