<script>
import Ps from 'perfect-scrollbar';
import erd from 'element-resize-detector';
import 'perfect-scrollbar/css/perfect-scrollbar.css'

export default {
    props: {
        scrollX: {
            type: Boolean,
            default: true
        },
        scrollY: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            ps: null,
            resizeDetector: erd({
                strategy: "scroll"
            })
        }
    },
    methods: {
        onResize() {
            if (this.ps) {
                this.ps.update(this.$refs.container);
            }

        }
    },
    mounted() {
        this.ps = new Ps(this.$refs.container, {
            suppressScrollX: !this.scrollX,
            suppressScrollY: !this.scrollY
        });

        // Ps.initialize(this.$refs.container, {
        //     suppressScrollX: !this.scrollX,
        //     suppressScrollY: !this.scrollY
        // });
        this.resizeDetector.listenTo(this.$refs.container, this.onResize);

        for (let i = 0; i < this.$slots.default.length; i++) {
            let el = this.$slots.default[i].elm;
            this.resizeDetector.listenTo(el, this.onResize);
        }
    },
    beforeUnmount() {
        this.resizeDetector.uninstall(this.$refs.container);

        for (let i = 0; i < this.$slots.default.length; i++) {
            let el = this.$slots.default[i].elm;
            this.resizeDetector.uninstall(el);
        }
    }
}
</script>

<template>
    <div ref="container" class="scrollable">
        <slot></slot>
    </div>
</template>
