
import Remote from "@/remote/scheduleRequest";
import {emitter} from "../../main";
import moment, {Moment} from "moment";
import EmployeeSchedule from "./EmployeeSchedule.vue";
import SearchEmployee from "./SearchEmployee.vue";
import Dialog from '@/components/dialog';
import {defineComponent} from "vue";
import DatePicker from "@/components/common/DatePicker.vue";

export default defineComponent({
    name: 'employeeAllocation',
    components: {
        SearchEmployee,
        EmployeeSchedule,
        DatePicker,
    },
    data: function () {
        return {
            bulkEmpList: <any[]>[],
            schedList: <any[]>[],
            hasChanges: false,

            // select schedule
            cloneSchedList: <any[]>[],
            selectSearchInput: '',

            // form model
            schedAllocation: {
                WorkschedId: -1,
                DateFrom: <Moment | null>moment(),
                DateTo: <Moment | null>null,
                Comment: '',
                EndExistingScheds: true
            },

            selectedSchedule: <any>{},

            // employee-chedule component input data
            selectedEmpForDetail: null,

            // datePicker
            startDateMenu: false,
            toDateMenu: false,
            startDatePickerValue: <any>'',
            toDatePickerValue: <any>'',
            datePickerTargetDay: <any>null,
            dateType: '',

            //
            isMobileView: false,

            //date modal
            dateModalTop: null,
            dateModalLeft: null,
        };
    },

    // created: function () {
    //
    // },
    mounted: function () {
        this.isMobileView = this.checkIsMobileView()
        // Dialog.loading()
        Remote.StandardSchedules().then(response => {
            this.schedList = response.Data.filter((data: any) => data.Archive === false && data.ScheduleType === 0);
            this.cloneSchedList = this.schedList.slice();
            //  Dialog.clear()
        })
        this.$watch('schedAllocation', (newVal: any, oldVal: any) => {
            if (newVal === oldVal) {
                this.hasChanges = true; //ignore employee list changes.
            }
        }, {deep: true});
    },
    computed: {
        selectedSched() {
            if (this.schedAllocation.WorkschedId === -1) {
                return false;
            }

            // todo: cant not use this.schedList in return
            let schedList: any = this.schedList;
            return schedList.find((sched: any)=> {
                return sched.Id === this.schedAllocation.WorkschedId
            })
        },

        capDate: function () {
            if (!this.schedAllocation.DateFrom) {
                return
            }

            let schedAllocation: any = this.schedAllocation

            let date = moment(schedAllocation.DateFrom);
            let yesterday = date.subtract(1, 'day')
            return yesterday.format('DD/MM/YYYY');
        },
    },

    methods: {
        // todo: fix calendar
        // getDay(date) {
        //     const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
        //
        //     let i = new Date(date).getDay(date)
        //     return daysOfWeek[i]
        // },

        navDownList() {
            console.log('nav down list')
        },

        updateSearchList(event: any) {
            if (event.key === "ArrowDown") {
                (this.$refs.selectDropdownRef as any).focus();
            }
            this.cloneSchedList = this.schedList.filter(o => {
                return o.Name.toLowerCase().indexOf(this.selectSearchInput.toLowerCase()) > -1
            })
        },

        updateDateText(event: any, target: any) {
            this.startDateMenu = false
            this.toDateMenu = false;
            target.set(moment(event, 'DD/MM/YYYY').toObject())
            // todo: add check for is correct format
            this.datePickerFormattedFromDate();
            this.datePickerFormattedToDate();
        },

        datePickerFormattedFromDate() {
            this.startDatePickerValue = this.schedAllocation.DateFrom ? moment(this.schedAllocation.DateFrom).format('YYYY-MM-DD') : undefined; // Date picker objects needs date in this particular format
        },
        datePickerFormattedToDate() {
            this.toDatePickerValue = this.schedAllocation.DateTo ? moment(this.schedAllocation.DateTo).format('YYYY-MM-DD') : undefined; // Date picker objects needs date in this particular format
        },

        updateEmpList(empList: any) {
            this.bulkEmpList = empList
        },
        startDateDatePickerHandler(date: any) {
            if (typeof date === 'string')
                date = moment(date);
            this.schedAllocation.DateFrom = date;
            this.datePickerFormattedFromDate()
        },
        toDateDatePickerHandler(date: any) {
            if (typeof date === 'string')
                date = moment(date);
            this.schedAllocation.DateTo = date;
            this.datePickerFormattedToDate()
        },

        removeEmployee: function (emp: any) {
            (this.$refs.SearchEmployeeRef as any).removeEmployee(emp)
        },

        clearSelectedEmployee: function () {
            if (this.hasChanges) {
                Dialog.checkChange({
                    message: 'There are currently Schedule Allocation changes <br /> Do you want to discard your changes?',
                    onOk: () => {
                        (this.$refs.SearchEmployeeRef as any).clearSelectedEmployeeList();
                        this.resetSchedAllocation()
                    }
                })
            } else {
                (this.$refs.SearchEmployeeRef as any).clearSelectedEmployeeList();
            }
        },

        allocateEmployees: function () {
            var warningMsgs = '';
            if (this.selectedSchedule.Id < 1 || !this.selectedSchedule.Id) {
                warningMsgs += '<br />Select a schedule that you want to allocate.';
            }

            if (this.schedAllocation.DateFrom && this.schedAllocation.DateTo &&
                this.schedAllocation.DateFrom.diff(this.schedAllocation.DateTo) > 0) {
                warningMsgs += '<br />Date To cannot be before Date From.';
            }

            if (warningMsgs != '') {
                Dialog.alert(warningMsgs)
                // emitter.emit('dialog', {
                //     message: warningMsgs,
                //     showButtons: true,
                //     noClose: false,
                //     showLoader: false
                // });

                return;
            }

            var postData = {
                WorkschedId: this.selectedSchedule.Id,
                EmployeeList: <any[]> [],
                DateFrom: moment(this.schedAllocation.DateFrom) ? moment(this.schedAllocation.DateFrom).format('DD/MMM/YYYY') : null,
                DateTo: moment(this.schedAllocation.DateTo) ? moment(this.schedAllocation.DateTo).format('DD/MMM/YYYY') : null,
                Comment: this.schedAllocation.Comment,
                EndExistingScheds: this.schedAllocation.EndExistingScheds
            };

            if (this.bulkEmpList.length > 0) {
                this.bulkEmpList.forEach((emp: any) => {
                    postData.EmployeeList.push(emp.EmployeeNo);
                });

            }

            // todo: did not find this.selectedEmp ??!!
            // else if (this.selectedEmp) {
            //     postData.EmployeeList = [this.selectedEmp.EmployeeNo];
            // }

            Dialog.loading()
            Remote.AllocateSchedule(postData).then(res => {
                this.hasChanges = false;
                this.resetSchedAllocation();
                Dialog.clear()
                if (this.$refs.EmployeeScheduleRef) {
                    (this.$refs.EmployeeScheduleRef as any).getEmpWorkScheds()
                }

                // this.getEmpWorkScheds
            })
                .catch(err => {
                    Dialog.alert(err.message)
                    console.error('err', err)
                })
        },

        closeSchedAllocation: function () {
            if (this.hasChanges) {
                Dialog.checkChange({
                    message: 'You have unsaved changes. Are you sure to discard the changes?',
                    onOk: () => {
                        this.resetSchedAllocation();
                        this.hasChanges = false;
                        (this.$refs.SearchEmployeeRef as any).clearSelectedEmployeeList();
                    },
                })
            } else {
                (this.$refs.SearchEmployeeRef as any).clearSelectedEmployeeList();
            }
        },

        showScheduleDetail: function (employee: any) {
            this.selectedEmpForDetail = employee;
        },

        resetSelectedEmpForDetail() {
            this.selectedEmpForDetail = null;
        },

        resetSchedAllocation: function () {
            this.hasChanges = false;
            this.schedAllocation = {
                WorkschedId: -1,
                DateFrom: moment(),
                DateTo: null,
                Comment: '',
                EndExistingScheds: true
            };
        },

        checkIsMobileView: function () {
            if (navigator.userAgent.match(/Android/i)
                || navigator.userAgent.match(/webOS/i)
                || navigator.userAgent.match(/iPhone/i)
                || navigator.userAgent.match(/iPad/i)
                || navigator.userAgent.match(/iPod/i)
                || navigator.userAgent.match(/BlackBerry/i)
                || navigator.userAgent.match(/Windows Phone/i)
            ) {
                return true;
            } else {
                return false;
            }
        },

        chooseSchedule($event: any, option: any) {
            $event.target.blur()
            this.selectedSchedule = option;
            this.selectSearchInput = this.selectedSchedule.Name;

            let a = $event.target.blur();

            let b = (this.$refs.selectRef as any);
            b.blur();
            let c = (this.$refs.selectInputRef as any);
            c.blur();
        },

        updateDateTime($event: any, dateFrom: any, dateType: string) {
            let element = $event.target.getClientRects()[0]
            this.dateModalTop = element.bottom;
            this.dateModalLeft = element.left;
            this.datePickerTargetDay = dateFrom;
            this.dateType = dateType;
            this.openDatePicker()
        },

        async openDatePicker() {
            // (this.$refs.DatePickerRef as any).toggleShow();
            let result = await ((this.$refs.DatePickerRef as any).toggleShow(this.datePickerTargetDay));
            if (this.dateType === 'DateTo') {
                this.schedAllocation.DateTo = result
            } else if (this.dateType === 'DateFrom') {
                this.schedAllocation.DateFrom = result
            }

        },

        submitForm() {
            //
        }
    },
})
