
import {defineComponent, ref} from "vue";
import {useStore} from "vuex";
import {FlexScheduleCellSetting} from "@/store/flexScheduleModule";

export default defineComponent({
    name: "FlexScheduleCellControl",
    setup() {
        const store = useStore()
        const open = ref(false)
        const optionSelected = ref([])

        const cellSettings: FlexScheduleCellSetting = store.state.FlexSchedule.flexScheduleCellSetting
        return {open, cellSettings, optionSelected}
    },
    methods: {
        optionChanged() {
            // update user config in local storage
            localStorage.setItem('flexScheduleCellSetting', JSON.stringify(this.cellSettings))
        },

        checkOptions(option: any) {
            // todo: trigger when user click on the row
        }
    }

})
