<!--<style src="./scheduleDetail.scss" lang="scss"></style>-->
<template>
    <div class="section default-form sched-empSched-form mt-4" v-if="empSchedDetail && empSchedDetail.Detail">
        <h3>Schedule Details</h3>
        <form v-on:submit.prevent="submitForm">
            <div class="caption mt-2">
                <div class="form-label">Name</div>
                <div class="label-empSched">{{ empSchedDetail.Detail.Name }}</div>
            </div>
            <div class="caption d-flex mt-4">
                <div class="form-label">Number of Weeks</div>
                <div class="label-empSched">{{ empSchedDetail.Detail.NoOfWeeks }}</div>
            </div>
            <div class="d-flex justify-space-between">
                <div class="caption mt-4">
                    <div class="date-label">Date From</div>

                    <input :value="$filters.reformatDate(empSchedDetail.DateFrom)"
                           v-bind="attrs" v-on="on"
                           outlined dense hide-details
                           autocomplete="off"
                           class="d-inline-flex ma-0 pa-0 mr-2 caption date-input-width date-input"
                           append-icon="icon-calendar"
                           @change="updateDateText($event, empSchedDetail.DateFrom)"
                           @click="updateDateTime($event, empSchedDetail.DateFrom, 'DateFrom')"
                           @click:clear="fromDate = false">
                    <!--                    <v-menu v-model="fromDateMenu" bottom offsetY-->
                    <!--                            :close-on-content-click="false">-->
                    <!--                        <template v-slot:activator="{ on, attrs }">-->
                    <!--                            <input :value="empSchedDetail.DateFrom | date"-->
                    <!--                                          v-bind="attrs" v-on="on"-->
                    <!--                                          autocomplete="off"-->
                    <!--                                          outlined dense hide-details-->
                    <!--                                          append-icon="icon-calendar"-->
                    <!--                                          @change="updateDateText($event, empSchedDetail.DateFrom)"-->
                    <!--                                          class="d-inline-flex ma-0 pa-0 caption white date-input-width"-->
                    <!--                                          @click:clear="empSchedDetail.DateFrom = null">-->
                    <!--                            -->
                    <!--                        </template>-->
                    <!--                        <v-date-picker :value="fromDatePickerValue" @input="fromDateDatePickerHandler"-->
                    <!--                                       :weekday-format="getDay"-->
                    <!--                                       no-title @change="fromDateMenu = false"></v-date-picker>-->
                    <!--                    </v-menu>-->
                </div>
                <div class="caption mt-4">
                    <div class="date-label">Date To</div>
                    <input :value="$filters.reformatDate(empSchedDetail.DateTo)"
                           v-bind="attrs" v-on="on"
                           autocomplete="off"
                           outlined dense hide-details
                           class="d-inline-flex ma-0 pa-0 caption date-input-width date-input"
                           append-icon="icon-calendar"
                           @change="updateDateText($event,  empSchedDetail.DateTo)"
                           @click="updateDateTime($event, empSchedDetail.DateTo, 'DateTo')"
                           @click:clear="toDateMenu = false">
                    <!--                    <v-menu v-model="toDateMenu" bottom offsetY-->
                    <!--                            :close-on-content-click="false">-->
                    <!--                        <template v-slot:activator="{ on, attrs }">-->
                    <!--                            <input :value="empSchedDetail.DateTo | date"-->
                    <!--                                          v-bind="attrs" v-on="on"-->
                    <!--                                          autocomplete="off"-->
                    <!--                                          outlined dense hide-details-->
                    <!--                                          append-icon="icon-calendar"-->
                    <!--                                          @change="updateDateText($event, empSchedDetail.DateTo)"-->
                    <!--                                          class="d-inline-flex ma-0 pa-0 caption white date-input-width"-->
                    <!--                                          @click:clear="empSchedDetail.DateTo = null">-->
                    <!--                            -->
                    <!--                        </template>-->
                    <!--                        <v-date-picker :value="toDatePickerValue"-->
                    <!--                                       :weekday-format="getDay"-->
                    <!--                                       @input="toDateDatePickerHandler" no-title @change="toDateMenu = false"></v-date-picker>-->
                    <!--                    </v-menu>-->
                </div>
            </div>

            <div class="caption mt-4">
                <div class="date-label">Notes</div>
                <textarea class="caption schedule-textarea"
                          @keyup="hasChanges = true"
                          hide-details rows="2" outlined dense v-model="empSchedDetail.Comment">
                </textarea>
            </div>

            <div class="section-table ui-gridbox-scrollable-titles mt-4">
                <div class="aff-table sched-empSched-table">
                    <table class="sched-empSched-days">
                        <thead>
                        <tr>
                            <th>Week</th>
                            <th>Day</th>
                            <th class="text-center">Hours</th>
                            <th class="text-center">Start</th>
                            <th class="text-center">Finish</th>
                            <th>Shift</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr v-for="d in empSchedDetail.Detail.Days">
                            <td class="text-center">
                                <span v-show="isFirstDayOfWeek(d.DayOfWeek)">{{ d.Week }}</span>
                            </td>
                            <td>
                                <span class="mr-2 days-width">{{ d.DayNumber + 1 }}</span>
                                <span>{{ getDayOfWeek(d.DayOfWeek) }}</span>
                            </td>
                            <td class="text-center">{{ d.Hours }}</td>
                            <td class="text-center"><span class="px-2">{{ $filters.reformatTime(d.StartTime) }}</span></td>
                            <td class="text-center"><span class="px-2">{{ $filters.reformatTime(d.FinishTime) }}</span></td>
                            <td class="shift-select-td">{{ d.ShiftId }}</td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="schedules-buttons form-row d-flex justify-end mt-4">
                <button type="button" class="white--text small red text-capitalize mr-2"
                        @click="RemoveSchedule()">
                    <span class="icon-briefcase mr-1"></span><span>Remove</span>
                </button>
                <button type="button" class="white--text small grey text-capitalize mr-2" :disabled="!hasChanges"
                        @click="cancelChanges()">
                    <span class="icon-cancel mr-1"></span><span>Cancel</span>
                </button>
                <button type="button" class="white--text small green text-capitalize mr-1" :disabled="!hasChanges"
                        @click="updateEmpSched()">
                    <span class="icon-save mr-1"></span><span>Update</span>
                </button>
            </div>
        </form>
        <date-picker ref="DatePickerRef" :top="dateModalTop" :left="dateModalLeft"></date-picker>
    </div>
</template>
<script lang="ts">
import Vue, {defineComponent} from 'vue';
import moment from "moment";
import Remote from '@/remote/scheduleRequest'
import {emitter} from '@/main';
import Dialog from "@/components/dialog";
import DatePicker from "@/components/common/DatePicker.vue";

export default defineComponent({
    name: "emp-schedule-detail",
    props: ['empSched'],
    components: {
        DatePicker
    },
    data: function () {
        return {
            // datepicker
            dateModalTop: null,
            dateModalLeft: null,
            datePickerTargetDay: <any>null,
            dateType: '',

            empSchedDetail: <any>null,
            hasChanges: <null | boolean>false,
            fromDateMenu: false,
            toDateMenu: false,
            fromDatePickerValue: <any>'',
            toDatePickerValue: <any>'',

        }
    },
    computed: {},

    mounted: function () {
        this.$emit("dataChanged", this.hasChanges); // todo wtf is components? check what does this line do !!!
        this.getEmpSchedDetail()
    },
    watch: {
        ['empSched'](newVal, oldVal) {
            if (newVal != oldVal) {
                this.empSchedDetail = null;
                this.hasChanges = null;
                this.fromDateMenu = false;
                this.toDateMenu = false;

                this.getEmpSchedDetail()
            }
        }
    },
    methods: {
        // todo: fix calendar
        // getDay(date) {
        //     const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
        //
        //     let i = new Date(date).getDay(date)
        //     return daysOfWeek[i]
        // },
        getEmpSchedDetail() {
            Dialog.loading()
            Remote.EmpSchedDetailRequest(this.empSched.EmployeeNo + '/' + this.empSched.Id)
            .then(response => {
                this.empSchedDetail = response.Data;
                if (!this.empSchedDetail.Detail) {
                    return;
                }
                var week = 0;
                var date = moment(this.empSchedDetail.Detail.StartDate);
                // Add missing 0 days
                var total = this.empSchedDetail.Detail.NoOfWeeks * 7;
                var fullDays = new Array(total);
                for (var dayNum = 0; dayNum < total; dayNum++) {
                    if (dayNum % 7 == 0) {
                        week++;
                    }

                    fullDays[dayNum] = {
                        DayNumber: dayNum,
                        DayOfWeek: date.day(),
                        Week: week,
                        Date: moment(date),
                        Hours: 0,
                        StartTime: null,
                        FinishTime: null,
                        ShiftId: null
                    };

                    date.add(1, 'day')
                }

                this.empSchedDetail.Detail.Days.map(function (day: any) {
                    //day.Date = new Date(Date.parse(day.Date));
                    if (day.StartTime)
                        fullDays[day.DayNumber].StartTime = moment(day.StartTime);
                    if (day.FinishTime)
                        fullDays[day.DayNumber].FinishTime = moment(day.FinishTime);
                    fullDays[day.DayNumber].Hours = day.Hours;
                    fullDays[day.DayNumber].ShiftId = day.ShiftId;

                });

                this.empSchedDetail.Detail.Days = fullDays;
                this.empSchedDetail.Detail.StartDate = moment(this.empSchedDetail.Detail.StartDate);

                if (response.Data.DateFrom) {
                    this.empSchedDetail.DateFrom = moment(response.Data.DateFrom, 'YYYY-MM-DD')
                }


                if (response.Data.DateTo) {
                    this.empSchedDetail.DateTo = moment(response.Data.DateTo, 'YYYY-MM-DD')
                }

                this.datePickerFormattedFromDate();
                this.datePickerFormattedToDate();
            })
            .finally(() => Dialog.clear())
        },

        submitForm() {
            return false;
        },


        updateDateText(event: any, target: any) {
            // this.startDateMenu = false
            this.toDateMenu = false;
            target.set(moment(event, 'DD/MM/YYYY').toObject())
            // todo: add check for is correct format
            this.datePickerFormattedFromDate();
            this.datePickerFormattedToDate();
        },
        datePickerFormattedFromDate() {
            this.fromDatePickerValue = this.empSchedDetail.DateFrom ? moment(this.empSchedDetail.DateFrom).format('YYYY-MM-DD') : undefined; // Date picker objects needs date in this particular format
        },
        datePickerFormattedToDate() {
            this.toDatePickerValue = this.empSchedDetail.DateTo ? moment(this.empSchedDetail.DateTo).format('YYYY-MM-DD') : undefined; // Date picker objects needs date in this particular format
        },

        fromDateDatePickerHandler(date: any) {
            if (typeof date === 'string')
                date = moment(date);
            this.empSchedDetail.DateFrom = date;
            this.hasChanges = true;
            this.datePickerFormattedFromDate()
        },
        toDateDatePickerHandler(date: any) {
            if (typeof date === 'string')
                date = moment(date);
            this.empSchedDetail.DateTo = date;
            this.hasChanges = true;
            this.datePickerFormattedToDate();
        },

        isFirstDayOfWeek: function (dayNo: any) {
            var startDate = moment(this.empSchedDetail.Detail.StartDate);
            return dayNo == startDate.day();
        },
        getDayOfWeek: function (dayNo: any) {
            if (dayNo < 0 || dayNo > 6)
                return 'unknown';
            var days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
            return days[dayNo];
        },

        RemoveSchedule() {
            // should be dialog confirm
            Dialog.checkChange({
                message: 'Are you sure?',
                onOk: () => {
                    Dialog.loading()
                    Remote.RemoveEmployeeWorkSchedule(this.empSchedDetail.Id, this.empSched.EmployeeNo)
                        .then(res => {
                            //
                            this.$emit('deleted')
                        }).finally(() => Dialog.clear())

                }
            })
        },

        cancelChanges() {
            // this.reloadDetail();
            if (!this.hasChanges) {
                // this.$emit('reload');
                this.getEmpSchedDetail()
                return;
            }

            let msg = 'There is currently unsaved schedule changes <br /> Do you want to discard your changes?';
            emitter.emit('dialog', {
                message: msg,
                showButtons: true,
                showCancel: true,
                okColor: 'orange',
                onOk: () => {
                    // this.$emit('reload');
                    this.getEmpSchedDetail()
                    this.hasChanges = false;
                },
                onCancel: () => {
                    // do nothing
                }
            })
        },

        updateEmpSched() {
            var dateFrom = moment(this.empSchedDetail.DateFrom);
            var dateTo = moment(this.empSchedDetail.DateTo);

            if (dateFrom && dateTo &&
                dateFrom > dateTo) {
                Dialog.alert('<br />Date From cannot be before Date To.')
                return;
            }

            emitter.emit('dialog', {
                message: 'Updating Employee Schedule',
                showButtons: false,
                showLoader: true,
                noClose: false
            });
            Dialog.loading()

            var postData = {
                Id: this.empSchedDetail.Id,
                DateFrom: dateFrom ? dateFrom.format('DD/MMM/YYYY') : null,
                DateTo: dateTo ? dateTo.format('DD/MMM/YYYY') : null,
                Comment: this.empSchedDetail.Comment
            };

            Remote.UpdateEmpSchedRequest(postData, this.empSched.EmployeeNo)
                .then(res => {
                    Dialog.clear();
                    this.$emit('updated')
                })
                .catch(err => {
                    Dialog.alert(err.message)
                    console.error('err', err)
                })
        },
        updateDateTime($event: any, dateFrom: any, dateType: string) {
            let element = $event.target.getClientRects()[0]
            this.dateModalTop = element.bottom;
            this.dateModalLeft = element.left;
            this.datePickerTargetDay = dateFrom;
            this.dateType = dateType;
            this.openDatePicker()

        },

        async openDatePicker() {
            // (this.$refs.DatePickerRef as any).toggleShow();
            let result = await ((this.$refs.DatePickerRef as any).toggleShow(this.datePickerTargetDay));
            if (this.dateType === 'DateTo') {
                this.empSchedDetail.DateTo = result
            } else if (this.dateType === 'DateFrom') {
                this.empSchedDetail.DateFrom = result
            }
            this.hasChanges = true;

        }
    },
})
</script>

