import {emitter} from "../main";

const DIALOG_QUEUE = []

export interface ConfirmDialogInput {
    message: string,
    confirmButtonText?: string,
    cancelButtonText?: string,
}


function testDialog() {
    emitter.emit('dialog', {
        message: 'here is a test dialog',
        showButtons: true,
        showCancel: true,
        okColor: 'orange',
    })
}

function checkChange(input: any) {
    emitter.emit('dialog', {
        dialog: true,
        message: input.message,
        showCancel: true,
        showButtons: true,
        showLoader: false,
        confirmButtonText: input.confirmButtonText ? input.confirmButtonText : 'Yes',
        cancelButtonText: input.cancelButtonText ? input.cancelButtonText : 'No',
        onOk: () => {
            input.onOk();
        },
        onCancel: () => {
            if (input.onCancel) {
                input.onCancel();
            }
        },
    })
}

function confirmOrCancel(input: ConfirmDialogInput): Promise<any> {
    return new Promise((confirm, reject) => {
        emitter.emit('dialog', {
            dialog: true,
            message: input.message,
            showCancel: true,
            showButtons: true,
            showLoader: false,
            confirmButtonText: input.confirmButtonText ? input.confirmButtonText : 'Yes',
            cancelButtonText: input.cancelButtonText ? input.cancelButtonText : 'No',
            onOk: () => {
                // input.onOk();
                confirm('ok');
            },
            onCancel: () => {
                reject('cancel')
            },
        })
    })
}

function alert(message: string) {
    emitter.emit('dialog', {
        dialog: true,
        message: message,
        showLoader: false,
        showButtons: true,
        onOk: () => {
            // do nothing
        },
        onCancel: () => {
            // do nothing
        }
    })

}

function loading(message?: string) {
    emitter.emit('dialog', {
        dialog: true,
        message: message ? message : "Loading",
        showCancel: false,
        showButtons: false,
        showLoader: true,
    })
    // DIALOG_QUEUE.push('message')
}

function clear() {
    // DIALOG_QUEUE.pop();
    // if (DIALOG_QUEUE.length === 0) {
    emitter.emit('clearDialog')
    // }
}

export default {
    testDialog,
    loading,
    clear,
    checkChange,
    alert,
    confirmOrCancel
}