<style src="./mySchedules.scss" lang="scss"></style>
<template>
    <div class="mt-4 mt-md-10">

        <div class="my-schedules-search-filter d-flex flex-wrap align-center mb-sm-4">
            <div class=" mr-2 mr-sm-6 date-searcher d-flex align-center">
                <div class="mr-2 d-inline caption bold date-searcher-label">Date From</div>

                <input :value="$filters.reformatDate(fromDate)"
                       class="d-inline-flex ma-0 pa-0 mr-2 caption" style="max-width:120px"
                       @change="updateDateText($event, fromDate, 'fromDate')"
                       @click="updateDateTime($event, fromDate, 'fromDate')"
                       @click:clear="fromDate = null">

                <button class="light-blue small px-aff-10 ml-2 lh-0"
                        @click="updateDateTime($event, fromDate, 'fromDate')"
                        v-tooltip.top="{content: 'Open calendar'}">
                    <span class="icon-popup white xs-icon"></span></button>

                <button class="green small px-aff-10 lh-0" @click="selectDateTodayForFromDate()"
                        v-tooltip.top="{content: 'Set to today'}">
                    <span class="icon-calendar white xs-icon"></span></button>
                <button class="orange small px-aff-10 lh-0" @click="fromDate=null"
                        v-tooltip.top="{content: 'Clear'}">
                    <span class="icon-cross white"></span></button>
            </div>
            <div class="date-searcher d-flex align-center ml-4">
                <div class="mr-2 d-inline caption bold date-searcher-label">Date To</div>
                <input :value="$filters.reformatDate(toDate)"
                       outlined dense hide-details
                       class="d-inline-flex ma-0 pa-0 mr-2 caption" style="max-width:120px"
                       @change="updateDateText($event, toDate, 'toDate')"
                       @click="updateDateTime($event, toDate, 'toDate')"
                       @click:clear="toDate = null">
                <button class="light-blue small px-aff-10 ml-2 lh-0" tile
                        @click="updateDateTime($event, toDate, 'toDate')"
                        v-tooltip.top="{content: 'Open calendar'}">
                    <span class="icon-popup white" x-small></span></button>
                <button class="green small px-aff-10 lh-0" tile @click="selectDateTodayForToDate()"
                        v-tooltip.top="{content: 'Set to today'}">
                    <span class="icon-calendar white" x-small></span></button>
                <button class="orange small px-aff-10 lh-0" tile @click="toDate = null"
                        v-tooltip.top="{content: 'Clear'}">
                    <span class="icon-cross white" x-small></span></button>
            </div>
            <button class="light-blue white--text ml-4 ml-sm-4 mt-sm-0 px-aff-10 small" @click="printWorkSchedule()">
                <span class="mr-1 icon-printer" x-small></span><span class="text-capitalize">Print</span>
            </button>
        </div>

        <div style="margin-top: 10px;">
            <div class="schedules-data-small" style="overflow:auto;">
                <div class="" v-for="schedule in schedules" style="width:100%;">
                    <div class="weekInfoDisplay py-2">
                        <div class="schedules-data-small-head">
                            <div>
                                <h3>{{ schedule.weekName }}</h3>
                            </div>
                            <div>{{ schedule.weekDateRange }}</div>
                        </div>

                    </div>
                    <div class="dayScheduleDisplay" v-for="(detail, index) in schedule.detailArr">
                        <div>
                            {{ schedule.weekName }} - {{ getWeekDayByIndex(index) }}
                        </div>

                        <div v-if="displayScheduleDetail(detail)">
                            <div>{{ detail.shiftDescription }}</div>
                            <div v-if="detail.startTime">Start Time:{{ detail.startTime }}</div>
                            <div v-if="detail.finishTime">Finish Time:{{ detail.finishTime }}</div>
                            <div v-if="detail.hours">Hours:{{ detail.hours }}</div>
                        </div>
                    </div>
                </div>
            </div>
            <div id="workSchedules" class="schedules-data-medium section" style="overflow:auto;">
                <div class="my-schedules aff-table">
                    <table>
                        <thead>
                        <tr>
                            <th>Week</th>
                            <th>Mon</th>
                            <th>Tue</th>
                            <th>Wed</th>
                            <th>Thu</th>
                            <th>Fri</th>
                            <th>Sat</th>
                            <th>Sun</th>
                        </tr>
                        </thead>
                        <tbody>
                            <tr class="myschedules-tr" v-for="schedule in schedules">
                                <td class="weekInfoRows">
                                    <div>{{ schedule.weekName }}</div>
                                    <div>{{ schedule.weekDateRange }}</div>
                                </td>

                                <td class="myschedules-td" v-for="detail in schedule.detailArr">
                                    <div v-if="displayScheduleDetail(detail)">
                                        <div>{{ detail.date }}</div>
                                        <div class="bold-text">{{ detail.shiftDescription }}</div>
                                        <div v-if="detail.hours">Hours:{{ detail.hours }}</div>
                                        <div v-if="detail.startTime">{{ detail.startTime }} -
                                            {{ detail.finishTime }}
                                        </div>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

            </div>
        </div>
        <date-picker ref="DatePickerRef"
                     :top="dateModalTop"
                     :leftInput="dateModalLeft"
                     :date="datePickerTargetDay"></date-picker>

    </div>

</template>
<script lang="ts">
import Remote from "@/remote/scheduleRequest";
import moment, {Moment} from 'moment';
import Dialog from '@/components/dialog';

import DatePicker from "@/components/common/DatePicker.vue";
import {defineComponent} from "vue";

enum DatePickerFormat {
    fromDate = 'fromDate',
    toDate = 'toDate'
}

export default defineComponent({
    name: 'mySchedules',
    components: {
        DatePicker,
        // FlexSchedule
    },
    data() {
        return {
            title: 'Work Schedule',

            schedules: [],
            user: null,
            datePickerFormat: "dd/MM/yyyy",

            //
            menu1: false,
            menu2: false,
            fromDate: <Moment | null> null,
            toDate: <Moment | null> null,

            //
            //  date picker
            dateModal: false,
            // datePickerDate: "",
            dateModalTop: 0,
            dateModalLeft: 0,
            datePickerTargetDay: moment(), // todo: clear moment and string mix
            selectedDateType: DatePickerFormat.fromDate, // Start Finish
        }
    },
    mounted() {

        var currentDate = new Date();
        currentDate.setHours(0, 0, 0, 0);
        var intDate = currentDate.getDate();
        var currentWeekDay = currentDate.getDay();
        var fromDate = new Date();
        fromDate.setHours(0, 0, 0, 0);
        var toDate = new Date();
        toDate.setHours(0, 0, 0, 0);

        var weekDayFactor = currentWeekDay == 0 ? 7 : currentWeekDay;
        fromDate.setDate(intDate - (weekDayFactor - 1));
        toDate.setDate(intDate + (7 - weekDayFactor));

        this.fromDate = moment(fromDate);
        this.toDate = moment(toDate);

        this.loadSchedules(this.fromDate, this.toDate);
    },

    methods: {
        updateDateText(event: any, target: any, type: string) {
            if (type === 'toDate') {
                this.toDate = moment(event.target.value, 'DD/MM/YYYY')
            } else {
                this.fromDate = moment(event.target.value, 'DD/MM/YYYY')
            }
            this.dateModal = false;

            // todo: add check for is correct format
        },

        updateDateTime($event: any, d: any, type: DatePickerFormat) {
            let element = $event.target.getClientRects()[0]
            this.dateModalTop = element.bottom;
            this.dateModalLeft = element.left;
            this.selectedDateType = type;
            if (type === 'toDate') {
                this.datePickerTargetDay = this.toDate ? this.toDate : moment();

            } else {
                this.datePickerTargetDay = this.fromDate ? this.fromDate : moment();
            }

            this.dateModal = true;
            this.openDatePicker()
        },

        printWorkSchedule: function () {
            var args = "left=5,top=5,resizable=yes,height=600,width=700,scrollbars=yes,status=1";
            var content = (document as any).getElementById("workSchedules").innerHTML;
            var mywindow: any = window.open('', 'Print', args);

            mywindow.document.write('<html>');
            mywindow.document.write('<head>');
            mywindow.document.write('<style type="text/css">table {border-collapse: collapse;}</style>');
            mywindow.document.write('<style type="text/css">table, th, td {border: 1px solid #cbcbcb;text-align: center;}</style>');
            mywindow.document.write('</head>');
            mywindow.document.write('<body>');
            mywindow.document.write(content);
            mywindow.document.write('</body></html>');

            mywindow.document.close();
            mywindow.focus()
            mywindow.print();
            mywindow.close();
        },
        loadSchedules: function (fromDate: any, toDate: any) {
            if (fromDate !== null) {
                fromDate.startOf('day')
            }

            if (toDate !== null && toDate !== '') {
                toDate.startOf('day')
            }

            this.schedules = [];

            if (toDate === null && fromDate === null) {
                return;
            }

            if (toDate !== null && fromDate === null) {
                fromDate = toDate;
            }

            if (toDate === null && fromDate !== null) {
                toDate = fromDate;
            }

            var param = {
                employeeNo: <number | null>null,
                fromDate: null,
                toDate: null
            };

            if (fromDate !== null && toDate !== null) {
                var finalFromDate = fromDate.format("DD/MM/YYYY");
                var finalToDate = toDate.format("DD/MM/YYYY");
                param = {
                    employeeNo: window.Affinity.user.EmployeeNo,
                    // employeeNo: 1148,
                    fromDate: finalFromDate,
                    toDate: finalToDate
                };
            }

            Dialog.loading();
            Remote.EmployeeWorkSchedule(param).then(response => {
                let tempSchedules = response;
                // server return json include key name 'mondayScheduleDetail' to 'sundayScheduleDetail'
                // transform it into a array so we can v-for it in correct order.
                for (let week of tempSchedules) {
                    week.detailArr = [];
                    week.detailArr.push(week.mondayScheduleDetail);
                    week.detailArr.push(week.tuesdayScheduleDetail);
                    week.detailArr.push(week.wednesdayScheduleDetail);
                    week.detailArr.push(week.thursdayScheduleDetail);
                    week.detailArr.push(week.fridayScheduleDetail);
                    week.detailArr.push(week.saturdayScheduleDetail);
                    week.detailArr.push(week.sundayScheduleDetail);

                    for (let detail of week.detailArr) {
                        // clear default value
                        if (detail.finishTime === "12:00 am" && detail.startTime === '12:00 am'
                            || detail.finishTime === "12:00 AM" && detail.startTime === '12:00 AM') {
                            detail.finishTime = '';
                            detail.startTime = '';
                        }

                        if (detail.hours === "0") {
                            detail.hours = '';
                            detail.shiftDescription = '';
                        }
                    }
                }

                this.schedules = tempSchedules;
                Dialog.clear();
            }).finally(() => {
                Dialog.clear();
            })
        },

        displayScheduleDetail(scheduleDetail: any) {
            return scheduleDetail.hours !== 0 && scheduleDetail.hours !== null && scheduleDetail.hours !== "";
        },

        selectDateTodayForFromDate: function () {
            this.fromDate = moment().startOf('day')
            this.toDate = null;
            this.loadSchedules(this.fromDate, this.toDate);
        },

        selectDateTodayForToDate: function () {
            this.toDate = moment();
            this.toDate.hour(11)
            this.toDate.minute(59)
            if (this.toDate < this.fromDate!) {
                this.fromDate = null;
            }
            this.loadSchedules(this.fromDate, this.toDate);
        },

        getWeekDayByIndex: function (index: number) {
            let weekdayArray = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'];
            return weekdayArray[index];
        },

        async openDatePicker() {
            let result = await ((this.$refs.DatePickerRef as any).toggleShow(this.datePickerTargetDay)) as Promise<any>;

            if (this.selectedDateType === DatePickerFormat.toDate) {
                this.toDate = result as any;
            } else if (this.selectedDateType === DatePickerFormat.fromDate) {
                this.fromDate = result as any;
            }

            this.loadSchedules(this.fromDate, this.toDate);

            return result;
        }
    }
})
</script>
