
import {emitter} from '../main'
import Modal from "@/components/common/Modal.vue";
import {defineComponent} from "vue";

export default defineComponent({
    name: "alert",
    components: {
      Modal
    },
    data() {
        return {
            dialog: false,
            message: 'some message ',
            showLoader: false,
            showButtons: false,
            showCancel: false,
            confirmButtonText: 'Ok',
            cancelButtonText: 'Cancel',
        }
    },
    mounted() {

        // console.log('mounted in alert')
        emitter.on('alert', (message) => {
            console.log('emitter alert')
            this.dialog = true;
            this.message = message;
            this.showLoader = false;
            this.showButtons = true;
        })

        emitter.on('dialog--loading--request', (message) => {
            console.log('emitter loading request')
            this.dialog = true;
            this.message = message;
            this.showCancel = false;
            this.showButtons = false;
            this.showLoader = true;
        })

        emitter.on('clearDialog', () => {
            console.log('emitter clear dialog')
            this.clearDialog();
            this.dialog = false;
        });
        emitter.on('dialog', (inputObj) => {
            console.log('emitter on dialog')
            this.toggleShow();
            this.dialog = !!inputObj;
            this.message = inputObj.message;
            this.showCancel = inputObj.showCancel;
            this.showButtons = inputObj.showButtons;
            this.showLoader = inputObj.showLoader;
            this.confirmButtonText = inputObj.confirmButtonText ? inputObj.confirmButtonText : 'Ok'
            this.cancelButtonText = inputObj.cancelButtonText ? inputObj.cancelButtonText : 'Cancel'

            if (inputObj.onOk) {
                this.onOk = () => {
                    inputObj.onOk();
                    this.dialog = false;
                    this.toggleShow()
                }
            }

            if (inputObj.onCancel) {
                this.onCancel = () => {
                    inputObj.onCancel();
                    this.dialog = false;
                    this.toggleShow()
                }
            }

        })
    },
    methods: {
        // onOk() {
        //     this.dialog = false;
        // },
        // onCancel() {
        //     this.dialog = false;
        // },
        clearDialog() {
            (this.$refs.alertModalRef as any).toggleShow(false);
        },
        toggleShow() {
            (this.$refs.alertModalRef as any).toggleShow();
        },
        onOk() {
            (this.$refs.alertModalRef as any).toggleShow();

        },
        onCancel() {
            (this.$refs.alertModalRef as any).toggleShow();

        },
    }
})
