
import Vue, {defineComponent} from 'vue';
import moment from "moment";
import Remote from '@/remote/scheduleRequest'
import {emitter} from '@/main';
import Dialog from "@/components/dialog";
import DatePicker from "@/components/common/DatePicker.vue";
import {EmployeeGetters} from "@/store/employeeModule";
import AutoComplete from "@/components/common/AutoComplete.vue";
import CustomSelect from "@/components/common/CustomSelect.vue";

export default defineComponent({
    name: "schedule-detail",
    components: {
        AutoComplete,
        DatePicker,
        CustomSelect
    },
    props: ['detail', 'shiftTypeList', 'readOnly'],
    data: function () {
        return {
            //
            createType: 'team',
            employees: [],

            // todo: bring back name length rule
            // nameLengthRule: [v => v.length <= 49 || 'Max 50 characters'],
            week: 1,
            tablePadding: '0 0;',
            hasChanges: false,
            isDateToCleared: false,
            shifts: <any[]>[],
            showShift: true,
            initialEmpSched: null,

            startDateMenu: false,

            //
            dateModalTop: 0,
            dateModalLeft: 0,

            //  time picker
            timePickerTargetDay: <any>null,
            selectedTimeType: 'Start', // Start Finish
        }
    },
    computed: {
        isNewSchedule() {
            let detail: any = this.detail
            return detail.Id <= 0;
        },
    },
    // created: function () {
    // },

    mounted: function () {
        // this.loadShiftTypes();
        this.shifts = this.shiftTypeList;

        this.hasChanges = this.detail.Id == -1; //new schedules always has changes

        this.$emit("dataChanged", this.hasChanges); // todo wtf is components? check what does this line do !!!


        let employees =  this.$store.getters[EmployeeGetters.getEmployees]

        employees = employees.filter((employee: any) => {
            return !this.$store.state.EmployeeModule.employeesWithPeronsalSche.includes(employee.employeeNo)
        })

        this.employees = employees.map((employee: any) => {
            employee.joinedName = employee.commonName + ' ' + employee.employeeNo
            return employee;
        })
        //

        //Add missing 0 days
        this.remapDays()

        console.log('schedule type', this.detail.ScheduleType)

        this.createType = this.detail.ScheduleType === 0 || this.detail.ScheduleType === undefined ? 'team' : 'personal';
    },
    watch: {
        ['detail'](newVal, oldVal) {
            this.remapDays()
            this.createType = this.detail.ScheduleType === 0 || this.detail.ScheduleType === undefined ? 'team' : 'personal';
            // todo: simplify this to create event
            if (newVal != oldVal && newVal.Id == -1) {
                this.detail.Days = [];
                this.createNewDays(1, 1);
            }
        }
    },
    methods: {
        selectedShift(selectedShift: any, d: any) {
            d.ShiftId = selectedShift;
            this.hasChanges = true;
        },
        formSubmited($event: any) {
            $event.preventDefault();
            $event.stopPropagation();
            return false
          //
        },
        increaseNumber() {
            this.detail.NoOfWeeks++;
            this.updateNoOfWeeks()
        },
        reduceNumber() {
            this.detail.NoOfWeeks--;
            this.updateNoOfWeeks()
        },
        toggleShift() {
            this.showShift = !this.showShift;
        },
        // todo: fix calendar
        // getDay(date: any) {
        //     const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
        //
        //     let i = new Date(date).getDay(date)
        //     return daysOfWeek[i]
        // },
        remapDays() {
            var week = 0;
            var date = moment(this.detail.StartDate);
            var total = this.detail.NoOfWeeks * 7;

            var fullDays = new Array(total);

            for (var dayNum = 0; dayNum < total; dayNum++) {
                if (dayNum % 7 == 0) {
                    week++;
                }

                fullDays[dayNum] = {
                    DayNumber: dayNum,
                    DayOfWeek: date.day(),
                    Week: week,
                    Date: moment(date),
                    Hours: 0,
                    StartTime: null,
                    FinishTime: null,
                    ShiftId: null
                };

                date.add(1, 'day')
            }

            this.showShift = false;
            this.detail.Days.map((day: any) => {
                if (day.StartTime)
                    fullDays[day.DayNumber].StartTime = moment(day.StartTime);
                if (day.FinishTime)
                    fullDays[day.DayNumber].FinishTime = moment(day.FinishTime);
                fullDays[day.DayNumber].Hours = day.Hours;
                fullDays[day.DayNumber].ShiftId = day.ShiftId;
            });
            this.detail.Days = fullDays;
            this.detail.StartDate = moment(this.detail.StartDate);
            this.hasChanges = false;
        },

        timeTextChanged(input: any, d: any, type: string) {
            let pickedTime = moment(input.target.value, 'HH:mm');

            if (type === 'FinishTime') {
                if (d.FinishTime && pickedTime.hour() === d.FinishTime.hour() && pickedTime.minute() === d.FinishTime.minute()) {
                    return
                }

                if (input === '') {
                    d.FinishTime = null;
                    return
                }

                d.FinishTime = pickedTime;
            } else {
                if (d.StartTime && pickedTime.hour() === d.StartTime.hour() && pickedTime.minute() === d.StartTime.minute()) {
                    return
                }

                if (input === '') {
                    d.StartTime = null;
                    return
                }

                d.StartTime = pickedTime;
            }

            this.timePickerTargetDay = d;
            this.autoCalcTime(type)

            this.hasChanges = true;
            // this.timeModal = false;
        },

        autoCalcTime(type: any) {
            let targetDay = this.timePickerTargetDay
            if (targetDay.FinishTime && targetDay.FinishTime.format('HH:mm') !== "00:00"
                && targetDay.StartTime && targetDay.StartTime.format('HH:mm') !== "00:00"
                && (!targetDay.Hours || targetDay.Hours === 0)) { // auto calc hours
                // set year month day to same
                targetDay.FinishTime.year(targetDay.StartTime.year())
                targetDay.FinishTime.month(targetDay.StartTime.month())
                targetDay.FinishTime.day(targetDay.StartTime.day())
                let timeDiff = targetDay.FinishTime.startOf('minute').diff(targetDay.StartTime.startOf('minute'), 'hours', true);

                targetDay.Hours = Math.round(timeDiff * 100) / 100;
            } else if (targetDay.FinishTime && targetDay.Hours
                && (!targetDay.StartTime || targetDay.StartTime.format('HH:mm') === "00:00")) { // auto calc start time
                targetDay.StartTime = targetDay.FinishTime.clone().subtract(targetDay.Hours, 'hours')
            } else if (targetDay.StartTime && targetDay.Hours
                && (!targetDay.FinishTime || targetDay.FinishTime.format('HH:mm') === "00:00")) { // auto calc finish time
                targetDay.FinishTime = targetDay.StartTime.clone().add(targetDay.Hours, 'hours')
            } else if (targetDay.FinishTime && targetDay.FinishTime.format('HH:mm') !== "00:00"
                && targetDay.StartTime && targetDay.StartTime.format('HH:mm') !== "00:00"
                && (targetDay.Hours || targetDay.Hours !== 0)) { // otherwise all blank filled.
                if (type === 'StartTime') {
                    targetDay.FinishTime = targetDay.StartTime.clone().add(targetDay.Hours, 'hours')
                } else if (type === 'FinishTime') {
                    targetDay.StartTime = targetDay.FinishTime.clone().subtract(targetDay.Hours, 'hours')
                }
            }
        },

        updateDateText(event: any, target: any) {
            this.startDateMenu = false
            target.set(moment(event, 'DD/MM/YYYY').toObject())
            // todo: add check for is correct format
            this.remapDays()
        },

        startDateDatePickerHandler(date: any) {
            if (typeof date === 'string')
                date = moment(date);
            this.detail.StartDate = date;
            this.remapDays()
        },

        isFirstDayOfWeek: function (dayNo: any) {
            var startDate = moment(this.detail.StartDate);
            return dayNo == startDate.day();
        },
        // loadShiftTypes: function () {
        //     var list = this.shiftTypeList;
        //
        //     for (var i = 0; i < list.length; i++) {
        //         if (list[i].ShiftType !== undefined &&
        //             list[i].ShiftType !== null &&
        //             list[i].ShiftType.length <= 5) {
        //             var shiftType: any = {
        //                 name: list[i].ShiftType
        //             };
        //             this.shifts.push(shiftType);
        //         }
        //     }
        //
        //     this.shifts.sort(function (a, b) {
        //         var x = a.name.toLowerCase();
        //         var y = b.name.toLowerCase();
        //         if (x < y) {
        //             return -1;
        //         }
        //         if (x > y) {
        //             return 1;
        //         }
        //         return 0;
        //     });
        // },

        getDayOfWeek: function (dayNo: number) {
            if (dayNo < 0 || dayNo > 6) {
                console.error('day No not correct')
                return 'unknown';
            }

            var days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thur', 'Fri', 'Sat'];
            return days[dayNo];
        },

        checkHour(targetDay: any) {
            this.hasChanges = true;

            if (targetDay.Hours > 24) {
                Dialog.alert('You can only enter up to 24 hours.')
                targetDay.Hours = 24
                return;
            }

            if (targetDay.StartTime && targetDay.Hours && targetDay.FinishTime) {
                targetDay.FinishTime = targetDay.StartTime.clone().add(targetDay.Hours, 'hours')
            } else if (targetDay.StartTime && targetDay.Hours) {
                targetDay.FinishTime = targetDay.StartTime.clone().add(targetDay.Hours, 'hours')
            } else if (targetDay.Hours && targetDay.FinishTime) {
                targetDay.StartTime = targetDay.FinishTime.clone().add(targetDay.Hours, 'hours')
            }
        },

        updateNoOfWeeks($event?: any) {
            let dayNum = this.detail.Days.length;
            let currentWeek = Math.floor(dayNum / 7);

            if (this.detail.NoOfWeeks > currentWeek) {
                this.createNewDays(currentWeek + 1, this.detail.NoOfWeeks)
            } else {
                let newLength = this.detail.NoOfWeeks * 7;
                this.detail.Days.splice(newLength);
            }
        },

        numLimitKeyDown: function (event: any, allowDecimal: boolean, allowColon: boolean) {
            let allow = [8, 9, 13, 46, 16, 17]; // backspace, del, tab, shift, ctrl, dot
            allow.push(65, 67, 86, 88); // a, c, v, x

            if (allowDecimal)
                allow.push(190, 110); //.

            if (allowColon) allow.push(186)

            if ((event.keyCode >= 48 && event.keyCode <= 57) || //key board numbers
                (event.keyCode >= 96 && event.keyCode <= 105) || //key pad numbers
                allow.indexOf(event.keyCode) > -1) { //various keys
                // all good!
            } else {
                event.preventDefault();
                event.stopPropagation();
            }
        },

        cancelChanges() {
            if (!this.hasChanges) {
                this.$emit('cancel');
                return;
            }

            let msg = 'You haven\'t saved your changes.  <br /> Are you sure you want to cancel?';
            Dialog.checkChange({
                message: msg,
                confirmButtonText: 'Yes',
                cancelButtonText: 'No',
                onOk: () => {
                    this.hasChanges = false;
                    this.$emit('cancel')
                }
            })

        },

        // todo: use reload to clean the changes
        reloadDetail: function () {
            if (!this.hasChanges)
                return;
            // toodo: add has change check
            this.$emit('reload');
        },

        createNewDays: function (fromWeek: any, toWeek: any) {
            if (fromWeek > toWeek)
                return;

            var week = fromWeek;
            var dayNum = this.detail.Days.length;
            while (week <= toWeek) {
                var date = moment(this.detail.StartDate);
                date.add(dayNum, 'day');
                this.detail.Days.push({
                    DayNumber: dayNum,
                    DayOfWeek: date.day(),
                    Week: week,
                    Date: date,
                    Hours: 0,
                    StartTime: null,
                    FinishTime: null,
                    ShiftId: null,
                    startDateModal: false,
                    endDateModal: false
                })
                dayNum++;
                week = Math.floor(dayNum / 7) + 1;
            }
        },

        archiveSchedule: function () {
            if (this.hasChanges)
                return;

            if (this.detail.Archive) {
                Dialog.alert('Unable to archive schedule, it\'s already archived.')
            } else {
                this.detail.Archive = true;
                this.doUpdateDetail();
            }
        },

        deleteSchedule: function () {
            if (this.hasChanges)
                return;

            if (this.detail.EmpAllocations.length > 0) {
                Dialog.alert('Unable to delete schedule, it\'s currently allocated to employees')
            } else {
                emitter.emit('dialog', {
                    message: "Are you sure you want to permanently delete this work schedule?",
                    showButtons: true,
                    showCancel: true,
                    okColor: 'red',
                    onOk: () => {
                        this.doDeleteSchedule();
                    },
                    onCancel: () => {
                    }
                })
            }
        },

        doDeleteSchedule: function () {
            Dialog.loading()

            Remote.DeleteSchedule(this.detail.Id).then(res => {
                this.$emit('schedule-deleted');
                Dialog.clear()
            })
        },

        updateDetail: function () {
            if (!this.hasChanges)
                return;

            if (!this.doUpdateValidation()) {
                return;
            }

            this.doUpdateDetail();
        },
        doUpdateValidation: function () {
            if (this.createType === 'team') {
                if ((!this.detail.Name && !this.detail.Description)
                    || (this.detail.Name === '' && this.detail.Description === '')) {
                    Dialog.alert('Schedule name cannot be blank. <br /> Schedule description cannot be blank.')
                    return false;
                }

                if (!this.detail.Name || this.detail.Name === '') {
                    Dialog.alert('Schedule name cannot be blank.')
                    return false;
                }
            }

            if (!this.detail.Description || this.detail.Description === '') {
                Dialog.alert('Schedule description cannot be blank.')
                return false;
            }

            if (this.detail.Name !== undefined &&
                this.detail.Name !== null &&
                this.detail.Name.length > 50) {
                Dialog.alert('Schedule names must be 50 characters or fewer (including spaces).')
                return false;
            }

            if (this.detail.Description !== undefined &&
                this.detail.Description !== null &&
                this.detail.Description.length > 50) {
                Dialog.alert('Schedule descriptions must be 50 characters or fewer (including spaces).')
                return false;
            }

            return true;
        },
        doUpdateDetail: function () {
            var postData = {
                Id: this.detail.Id,
                NoOfWeeks: this.detail.NoOfWeeks,
                StartDate: moment(this.detail.StartDate).format('DD/MMM/YYYY'),
                Name: this.createType === 'team' ? this.detail.Name : 'Personal_ ' + this.detail.EmployeeNo,
                EmployeeNo: this.detail.EmployeeNo ? this.detail.EmployeeNo : null,
                ScheduleType: this.detail.EmployeeNo ? 1 : 0,
                IsPersonal: !!this.detail.EmployeeNo,
                Description: this.detail.Description,
                Archive: this.detail.Archive,
                Days: <any[]>[]
            }

            this.detail.Days.map(function (day: any) {

                if (day.Hours > 0 ||
                    (day.ShiftId !== null && day.ShiftId !== "")) {
                    if (day.ShiftId === "") day.ShiftId = null;
                    postData.Days.push({
                        DayNumber: day.DayNumber,
                        StartTime: day.StartTime ? day.StartTime.format('HH:mm') : null,
                        FinishTime: day.FinishTime ? day.FinishTime.format('HH:mm') : null,
                        Hours: day.Hours > 24 ? 24 : day.Hours,
                        ShiftId: day.ShiftId,
                    });
                }
            });

            Dialog.loading()
            Remote.PostSchedule(postData).then(res => {
                this.$emit('schedule-saved', res.Data);
                if (this !== undefined &&
                    this.hasChanges !== undefined) {
                    this.hasChanges = false;
                }
                if (this !== undefined) {
                    // todo broken
                    // this.hasDataChanges = this.hasChanges;
                }
            }).finally(() => {
                Dialog.clear();
            })
        },
        formUpdated() {
            this.hasChanges = true;
        },

        async openDatePicker($event: any) {
            let element = $event.target.getClientRects()[0]
            this.dateModalTop = element.bottom;
            this.dateModalLeft = element.left;
            try {
                this.detail.StartDate = await (this.$refs.DatePickerRef as any).toggleShow(this.detail.StartDate);
            } catch (e) {
                e ? console.error(e) : null;
                return;
            }

            this.remapDays()
            this.hasChanges = true;
        },
        selectedEmployee(employee: any) {
            this.detail.EmployeeNo = employee.employeeNo
            this.detail.Description = `${employee.commonName} ${employee.employeeNo} Personal Schedule`
        }
    },

})
