<template>
    <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.0.0-beta1/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-giJF6kkoqNQ00vy+HMDP7azOuL0xtbfIcaT9wjKHr8RbDVddVHyTfAAsrekwKmP1" crossorigin="anonymous">
    <aside class="col-sm-4">
        <p>Login form style 3</p>

        <div class="card">
            <article class="card-body">
                <h4 class="card-title text-center mb-4 mt-1">Sign in</h4>
                <hr>
                <p class="text-success text-center">Some message goes here</p>
                <form v-on:submit.prevent="login">
                    <div class="form-group">
                        <div class="input-group">
                            <input name="" class="form-control" placeholder="Email or login" v-model="username">
                        </div>
                    </div>
                    <div class="form-group mt-2">
                        <div class="input-group">
                            <input class="form-control" placeholder="******" type="password" v-model="password">
                        </div>
                    </div>
                    <div class="form-group mt-4">
                        <button type="submit" class="btn btn-primary btn-block"> Login  </button>
                    </div>

                </form>
            </article>
        </div> <!-- card.// -->

    </aside>
</template>

<script>
import axios from "axios";

export default {
    name: "Login",
    data() {
        return {
            username: '',
            password: ''
        }
    },
    methods: {
        login() {
            console.log('login')
            console.log(this.username)
            console.log(this.password)

            axios.post('http://localhost:61160/api/Authentication/post', {
                username: this.username,
                password: this.password
            }).then(res => {
                console.log('res', res)
            })
        }
    }

}
</script>

