<template>
    <div class="section shadow sched-card-default pa-4">
        <h3>Select Employees</h3>
        <input class="mt-2 caption text-field-input" v-model="searchTerm" hide-details dense outlined type="text"
               placeholder="Search for employees "/>
        <div class="aff-table mt-4">
            <table class="ui-grid">
                <tbody>
                <tr v-for="emp in empList" :class="emp.classObject" @click="toggleEmployeeSelect(emp)">
                    <td>
                        <span class="name-text-width">{{ emp.EmployeeName }} ({{ emp.EmployeeNo }})</span>

                        <span class="current-schedules-width">
                            <div class="schedule-name" v-for="name in emp.WorkschedNames">{{ name }}</div>
                            <div class="schedule-name">{{ emp.PersonalSchedule }}</div>
                        </span>
                    </td>
                    <td>
                        <button v-if="!isMobileView && !emp.classObject.selected"
                                class="green white--text fsmall"
                                v-tooltip.right="{content: 'Add to Employee Selection List'}"
                                @click.stop="addEmployee(emp)">
                            <span class="icon-add-to-list"></span>
                        </button>
                        <button v-if="isMobileView && !emp.classObject.selected"
                                class="green white--text fsmall"
                                @click.stop="addEmployee(emp)">
                            <span class="icon-add-to-list"></span>
                        </button>
                        <button v-if="emp.classObject.selected"
                                class="grey fsmall"
                                v-tooltip.right="{content: 'Remove Employee From Selection List'}"
                                @click.stop="removeEmployee(emp)">
                            <span class="icon-minus white--text small"></span>
                        </button>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script lang="ts">
import {emitter} from "../../main";
import Remote from "@/remote/scheduleRequest";
import Vue, {defineComponent} from "vue";
import Dialog from '@/components/dialog'

export default defineComponent({
    name: "SearchEmployee",
    data: function () {
        return {
            // employee list
            searchTerm: '',
            empList: [],
            selectedEmpList: <any>[],
            selectedEmp: null,
            isMobileView: false,
        }
    },
    mounted() {
        if (navigator.userAgent.match(/Android/i)
            || navigator.userAgent.match(/webOS/i)
            || navigator.userAgent.match(/iPhone/i)
            || navigator.userAgent.match(/iPad/i)
            || navigator.userAgent.match(/iPod/i)
            || navigator.userAgent.match(/BlackBerry/i)
            || navigator.userAgent.match(/Windows Phone/i)
        ) {
            console.log('is mobile view true')
            this.isMobileView = true;
        } else {
            this.isMobileView = false;
        }

        this.getEmployeesList()
    },
    watch: {
        searchTerm: function (newVal, oldVal) {
            if (oldVal != newVal) {
                if (newVal.trim().length == 0) {
                    this.empList.forEach(function (emp: any) {
                        emp.classObject.visible = true;
                        emp.classObject.hidden = false;
                    });

                } else {
                    var searchTerm = newVal.toLowerCase().trim();
                    this.empList.forEach((emp: any) => {
                        emp.classObject.visible = (emp.EmployeeName && emp.EmployeeName.toLowerCase().indexOf(searchTerm) > -1) || (emp.EmployeeNo && emp.EmployeeNo.toString().indexOf(searchTerm) > -1);
                        emp.classObject.hidden = !emp.classObject.visible;
                    });
                }
            }
        }
    },
    methods: {
        getEmployeesList() {
            Dialog.loading()
            Remote.EmployeesList().then(response => {
                response.Data.map((sched: any, index: number) => {
                    sched.classObject = {
                        'visible': true,
                        'hidden': false,
                        'selected': false,
                        'odd': index % 2
                    }
                })
                this.empList = response.Data;
            }).finally(() => {
                Dialog.clear()
            })

        },
        toggleEmployeeSelect: function (emp: any) {
            if (emp.classObject.selected) {
                this.removeEmployee(emp)
            } else {
                this.addEmployee(emp)
            }
        },
        removeEmployee: function (emp: any) {
            emp.classObject.selected = false;
            this.selectedEmpList.splice(this.selectedEmpList.indexOf(emp), 1);
            this.$emit('updateSelectedEmpList', this.selectedEmpList)
        },

        addEmployee: function (emp: any) {
            emp.classObject.selected = true;
            this.selectedEmpList.push(emp);
            this.$emit('updateSelectedEmpList', this.selectedEmpList)
        },

        // external
        // called from parent
        clearSelectedEmployeeList: function () {
            this.selectedEmpList.forEach((emp: any) => {
                emp.classObject.selected = false;
            })

            this.selectedEmpList = [];
            this.$emit('updateSelectedEmpList', this.selectedEmpList)
        },
    }
})
</script>

<style lang="scss" scoped>
.sched-card-default {
    background: #efefef;
    width: 460px;

    @media only screen and (max-width: 600px) {
        width: 100%;
    }
}

.aff-table {
    max-height: 600px;
    overflow: auto;

    table {
        border: 1px solid #e3e3e3;
    }

    tr {
        cursor: pointer;
    }

    @media only screen and (max-height: 900px) {
        max-height: 600px;
    }

    @media only screen and (max-height: 800px) {
        max-height: 500px;
    }

    @media only screen and (max-height: 700px) {
        max-height: 400px;
    }

    @media only screen and (max-width: 600px) {
        max-height: 200px;
    }
}

.emp-selected-table {
    width: 80%;
}

.v-btn:not(.v-btn--round).v-size--small {
    height: 24px;
}

.name-text-width {
    display: inline-block;
    width: 200px;
    vertical-align: top;
    @media screen and (max-width: 600px) {
        width: 140px;
    }
}

.current-schedules-width {
    display: inline-block;
    width: 130px;
}

.schedule-name {
    width: 130px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
</style>