<template>
    <div id="sched-man-list " class="sched-man-list mt-4" v-show="!createNew">
        <div>
            <div class="d-flex align-center flex-wrap">
                <input v-model="filters.searchTerm" type="text" placeholder="Search for schedules" hide-details
                       class="sched-search-box body-2 underline-input"/>
                <div class="filter-dropdown">
                    <div class="custom-select" tabindex="1" ref="selectRef">
                        <div class="custom-select-selected">
                            {{filters.selected}}
                        </div>
                        <div class="custom-select-items">
                            <div class="custom-select-item"
                                 v-for="(option, index) in filters.options" :key="index"
                                 :tabindex="index+99"
                                 @click="choseFilter($event, option)">
                                <div class="cell-config">
                                    {{ option }}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <button class="blue ml-2 small" @click="createNewSchedule()">
                    <span class="icon-plus btn-icon white"></span>
                    <span class="font-weight-regular text-capitalize white">&nbsp;Create</span>
                </button>
            </div>
        </div>
        <div class="aff-table sche-search-table mt-4">
            <table class="ui-grid">
                <thead>
                <tr>
                    <th style="width: 120px" @click="updateSort(0)">Name <span
                        v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[0] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[0] === 'Desc'}"></span>
                    </th>
                    <th @click="updateSort(1)">Description <span
                        v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[1] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[1] === 'Desc'}"></span>
                    </th>
                    <th style="width: 70px" class="text-center" @click="updateSort(2)">Weeks <span
                        v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[2] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[2] === 'Desc'}"></span>
                    </th>
                    <th style="width: 60px" class="text-center" @click="updateSort(3)">H/W <span
                        v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[3] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[3] === 'Desc'}"></span>
                    </th>
                    <th style="width: 80px" class="text-center" @click="updateSort(4)">Allocated<span
                        v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[4] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[4] === 'Desc'}"></span>
                    </th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="sched in schedList" @click="selectSchedulesDetail(sched)"
                    v-bind:class="sched.classObject">
                    <td style="width: 50px;">
                        <div>{{ sched.Name }}</div>
                    </td>
                    <td style="width: 100px;"
                        v-tooltip.right="{content: sched.Description, delay: {show: 3000, hide: 100}}">
                        <!--                        v-tooltip.right="{content: sched.Description,-->
                        <!--                        class: 'tooltip-custom tooltip-other-custom'}"-->
                        <div class="sched-search-desc">
                            {{ sched.Description }}
                        </div>
                    </td>
                    <td class="text-center">
                        <div>{{ sched.NoOfWeeks }}</div>
                    </td>
                    <td class="text-center">
                        <div>{{ sched.HoursPerWeek }}</div>
                    </td>
                    <td class="text-center">
                        <div>{{ sched.Count }}</div>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script lang="ts">
import Vue, {defineComponent, inject} from "vue";
import Remote from "@/remote/scheduleRequest";
import moment from "moment";
import Dialog from '@/components/dialog'
import {StandardResponse} from "@/components/schedulesManagement/ScheduleManagementInterface";

export default defineComponent({
    name: "SearchSchedule",
    inject: {
        manageCompanyConfig: 'MANAGE_COMPANY_CONFIG'
    },

    data() {
        return {
            selectedSched: <any>null,
            schedList: <any[]>[],
            shiftTypeList: [],
            schedDetail: null,
            createNew: false,
            empListHeight: '',
            showFilters: true,
            // checkboxes

            filters: {
                searchTerm: '',
                sort: 'Name',
                sortOptions: ['Name', 'Description', 'NoOfWeeks', 'HoursPerWeek', 'Count'],
                orderBy: 'Asc',
                orderByArrow: ['Asc', 'Asc', 'Asc', 'Asc', 'Asc'],

                partTimeSche: false,
                fullTimeSche: true,
                activeSche: true,
                inActiveSche: false,
                archivedSche: false,
                options: <any[]>[],
                selected: 'Show Active',
                all: false,
            },
            searchTermFocus: false
        }
    },

    mounted: function () {
        this.showFilters = true;
        this.getSchedulesList();
        if ((this as any).manageCompanyConfig.getAllowPersonal()) {
            this.filters.options = ['Show All', 'Show Active', 'Show Inactive', 'Show Archived', 'Show Personal']
        } else {
            this.filters.options = ['Show All', 'Show Active', 'Show Inactive', 'Show Archived']
        }

        this.$watch('filters', this.refreshFilters, {deep: true});
    },
    methods: {
        choseFilter($event: any, selected: any) {
            $event.target.blur()

            this.filters.selected = selected;

            let a = $event.target.blur();

            let b = (this.$refs.selectRef as any);
            b.blur();
        },
        createNewSchedule() {
            this.$emit('createNewSchedule')
        },
        selectSchedulesDetail(sched: any) {
            this.schedList.map((sched: any) => {
                sched.classObject.selected = false;
            })
            sched.classObject.selected = true;
            this.selectedSched = sched;
            this.$emit('selectSchedFromSearch', this.selectedSched)
        },
        deselectScheDetail() {
            this.schedList.forEach((sched: any) => {
                sched.classObject.selected = false;
            })
            this.selectedSched = null;
        },

        toggleFilters: function () {
            this.showFilters = !this.showFilters;
        },
        refreshFilters: function () {

            //Sort
            var vm: any = this;

            this.schedList.sort(function (x: any, y: any) {
                var order = 1;

                if (vm.filters.orderBy === 'Desc')
                    order = -1;
                var xString
                var yString
                if (typeof x[vm.filters.sort] === 'string') {
                    xString = x[vm.filters.sort].toLowerCase();
                    yString = y[vm.filters.sort].toLowerCase();
                } else {
                    xString = x[vm.filters.sort];
                    yString = y[vm.filters.sort];
                }


                if (xString < yString) {
                    return -1 * order;
                } else if (xString > yString) {
                    return 1 * order;
                } else {
                    return 0;
                }
            })

            var searchTerm = this.filters.searchTerm.toLowerCase().trim();
            this.schedList.forEach((sched: any) => {
                sched.classObject.selected = vm.selectedSched ? vm.selectedSched.Id == sched.Id : false;
                sched.classObject.visible = true;

                // if all filter are selected
                if (this.filters.all) {
                    sched.classObject.visible = true
                } else {
                    //Type filter
                    // sched.classObject.visible = this.getCheckboxFilterResult(sched);
                    sched.classObject.visible = this.getDropDownFilterResult(sched);
                    //search term
                    if (sched.classObject.visible && searchTerm.length != 0) {
                        sched.classObject.visible = (sched.Name.toLowerCase().indexOf(searchTerm) !== -1
                            || sched.Description.toLowerCase().indexOf(searchTerm) !== -1) && sched.classObject.visible;
                    }
                }

                sched.classObject.hidden = !sched.classObject.visible;
            });
        },

        getDropDownFilterResult(sched: any) {
            // All', 'Active', 'InActive', 'Archived'
            switch (this.filters.selected) {
                case 'Show All':
                    return true;
                case 'Show Active':
                    return sched.isActive === true && sched.Archive === false && sched.ScheduleType === 0;
                case 'Show Inactive':
                    return sched.isActive === false && sched.Archive === false && sched.ScheduleType === 0;
                case 'Show Archived':
                    return sched.Archive === true && sched.ScheduleType === 0;
                case 'Show Personal':
                    return sched.ScheduleType === 1;
            }
        },

        getSchedulesList: function () {
            Dialog.loading()
            Remote.StandardSchedules()
            .then((response: StandardResponse) => {
                var weeks = [];
                let employeeNoList: number[] = [];
                let filteredSchedule = response.Data;

                if (!(this as any).manageCompanyConfig.getAllowPersonal()) {
                    filteredSchedule = response.Data.filter((sched: any) => {
                        return sched.ScheduleType === 0
                    })
                }

                filteredSchedule.map((sched: any) => {

                    // parse Peronsal_10005 to find employee id
                    if (sched.Name.includes('Personal')) {
                        var thenum = sched.Name.replace( /^\D+/g, '');
                        employeeNoList.push(Number(thenum))
                    }

                    sched.classObject = {
                        selected: false,
                        visible: true,
                        hidden: false
                    };

                    if (this.selectedSched && this.selectedSched.Id === sched.Id) {

                        this.selectedSched = sched;
                        sched.classObject.selected = true;
                    }

                    weeks.push(sched.NoOfWeeks)

                })
                weeks.unshift('All');
                this.schedList = filteredSchedule;
                this.$store.state.EmployeeModule.employeesWithPeronsalSche = employeeNoList;
                this.refreshFilters();
                Dialog.clear();
            })
        },
        updateSort: function (sortIndex: number) {
            this.filters.orderBy = this.filters.orderByArrow[sortIndex] = this.filters.orderByArrow[sortIndex] === 'Asc' ? 'Desc' : 'Asc';
            this.filters.sort = this.filters.sortOptions[sortIndex];
            // reset other arrows;
            this.filters.orderByArrow.forEach((arrow, index) => {
                if (index !== sortIndex) {
                    arrow = 'Asc'
                }
            })
        },

    }
})
</script>

<style scoped lang="scss">
@use '../../style/colors';

.sched-man-list {
    max-height: 400px;
    max-height: 80vh;
    min-height: 100px;

    .section-table {
        height: 90%;
        padding-bottom: 20px;
        overflow: auto;
    }

    @media screen and (max-width: 1200px) {
        /*height: 200px;*/
    }
}

.sched-search-box {
    margin-right: 16px;
    flex-grow: 1;
    @media screen and (max-width: 600px) {
        width: 100%;
        margin-right: 0;
    }
}

.emp-sched-list {
    margin-bottom: 20px;
    margin-right: 30px;
    overflow-y: auto;
    max-height: 300px;
    min-height: 140px;
}

.sched-man-filters {
    margin: 0 10px 10px 0;

    .label-width-120 {
        display: inline-block;
        width: 120px;
    }

    .label-width-70 {
        display: inline-block;
        width: 70px;
    }

    .v-input {
        display: inline-flex;
        font-size: 12px;
    }

    span {
        margin-right: 5px;
    }

    select {
        width: auto;
        padding: 2px 5px;
    }
}

.sche-search-table {
    overflow: auto;
    max-height: calc(100vh - 300px);

    table {
        min-width: 530px;
        table-layout: fixed;

        tr {
            cursor: pointer;
        }

        td {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    @media screen and (max-width: 600px) {
        max-height: 230px;
    }
}

.sched-search-desc {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: inline-block;
    line-height: 1;
    vertical-align: middle;
}

.filter-dropdown {
    width: 120px;
}

.cell-config {
    pointer-events: none;
}

.custom-select {
    font-size: 12px;
}

.custom-select-selected {
    padding: 4px 1.5em 4px 0.2em;
    border: none;
    border-radius: 0;
    background-color: transparent;
    border-bottom: 1px solid rgba(colors.$grey, 0.7);

}

.custom-select-selected:after {
    top: 8px;
    right: 0;
}

.custom-select:focus-within {
    .custom-select-selected {
        border: none;
        border-radius: 0;
        border-bottom: 1px solid colors.$aff-dark-blue;
    }

    .custom-select-selected:after {
        transform: rotate(180deg);
        top: 4px;
    }
}

.custom-select-items {
    background: white;
    width: max-content;
}

.custom-select-item {
    color: colors.$dark-blue-app;
    // padding-left: 1em;
    cursor: pointer;
    user-select: none;
    padding: 4px 1em;
}

.custom-select-item:hover {
    color: colors.$aff-dark-blue;
    background-color: lighten(colors.$aff-blue, 50%);
}

</style>