const shortWeekDays = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
const fullWeekDays = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday']
const frequencyCycle = ['Once', 'Weekly', 'Fortnightly']

function findFullDaysByShort(shortDay: string) {
    return fullWeekDays[shortWeekDays.indexOf(shortDay)]
}

function findShortDayByFull(fullDay: string) {
    return shortWeekDays[fullWeekDays.indexOf(fullDay)]
}

const AvailabilityLookup = {
    shortWeekDays,
    fullWeekDays,
    frequencyCycle,
    findFullDaysByShort,
    findShortDayByFull
}

export default AvailabilityLookup;