import Vue from "vue";
// import utils from "../utils";
import axios from "axios";
import moment from "moment";
import Dialog from '@/components/dialog'
import {Leave, RemoteFlexScheDetail} from "@/remote/flexSchedulesRequest";
import {EMPPosition} from "@/store/lookupModule";
import {ManagedEmployee} from "@/store/employeeModule";

let ISDEV = false;
//
if (process.env.VUE_APP_AXIOSHOST) {
    ISDEV = true;
    axios.defaults.baseURL = process.env.VUE_APP_AXIOSHOST
}
//

axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

axios.interceptors.response.use(resp => {
    return resp;
}, (err) => {
    if (err && err.response && err.response.status == 401) {
        location.reload();
    }
    return Promise.reject(err);
});

let EmployeeWorkSchedule = function (data: any) {
    return axios.post('EmployeeWorkSchedule', data).then(res => {
        if (typeof res.data === "string" && res.data !== "") {
            return JSON.parse(res.data)
        }

        return res.data
    })
}

let AuthGetInfo = function () {
    return axios.post('Auth/GetInfo').then(res => {
    // return axios.post('http://localhost:3000/Auth/GetInfo').then(res => {
        return res.data
    })
}

if (ISDEV) {
    // for dev with dummy data
    EmployeeWorkSchedule = function (data) {
        console.log('data', data)
        return axios.get('EmployeeWorkSchedule', data).then(res => {
            if (typeof res.data === "string" && res.data !== "") {
                return JSON.parse(res.data)
            }

            return res.data
        })
    }

    AuthGetInfo = function () {
        return axios.get('Auth/GetInfo').then(res => {
            return res.data
        })
    }
}

function StandardSchedules() {
    return axios.get('StandardSchedules').then(res => {
        return res.data
    })
}

async function ShiftTypes() {
    return axios.get('ShiftTypes').then(res => {
        return res.data
    })
}

function ScheduleDetail(id: number) {
    return axios.get(`ScheduleDetail/${id}`).then(res => {
        return res.data
    })
}


