
import {ScheduleDay} from "@/components/FlexSchedule/schedule-day-mock";
import {computed, defineComponent, PropType, reactive} from "vue";
import {FlexScheduleCellSetting, FlexScheduleMutation} from "@/store/flexScheduleModule";
import {useStore} from "vuex";
import EditableCellElement from "@/components/FlexSchedule/EditableCellElement.vue";
import {LookupActions, ShiftType} from "@/store/lookupModule";

export default defineComponent({
    name: "DynamicScheCell",
    components: {
        EditableCellElement
    },
    props: {
        scheDay: {
            type: Object as PropType<ScheduleDay>
        }
    },
    data() {
        return {
            editTime: false,
            editing: false,
        }

    },
    setup() {
        const store = useStore()

        const cellSettings: FlexScheduleCellSetting = store.state.FlexSchedule.flexScheduleCellSetting

        const shiftOptions = computed(() => {
                return store.state.LookUp.shifts
            })

        const positionOptions = computed(() => {
            return store.state.LookUp.positions
        })


        return {cellSettings,
                shiftOptions,
                positionOptions,
                selectedShift: computed((shiftId: string) => {
                    return shiftOptions.value.find((option: ShiftType) => {
                        return option.shiftType === shiftId
                    })
                }),
                // positionOptions: computed(() {
                //     let options = this.$store.state.LookUp.positions;
                //     if (options.length === 1) {
                //         (this as any).selectedPosition = options[0];
                //     }
                //     return options
                // }),
        }
    },
    methods: {
        updateElement($event: any, scheDay: any, key: any) {
            // console.log('update event', $event)
            // console.log('update target', scheDay)
            // console.log('update key', key)
            scheDay.startTime = $event;
        },
        moreDetail(day: ScheduleDay) {
            // console.log('day', day)
            this.$store.commit(FlexScheduleMutation.setScheduleDay, day)
            this.$emit('openEditDialog', true)
        },

        updateEditTime(isEditing: boolean) {
            this.editTime = isEditing;
            if (isEditing) {
                this.editing = true;
            }
        },

        createSche(scheDay: ScheduleDay) {
            // this.$store.commit(FlexScheduleMutation.setTargetEmployee)
            this.$store.dispatch(LookupActions.getPositions, (scheDay as any).employeeNo)
            this.$store.commit(FlexScheduleMutation.createNewScheduleDay, scheDay)
            this.$emit('openEditDialog', false)
        },

        updateEditing(isEditing?: boolean) {
            if (isEditing === true || isEditing === false) {
                this.editing = isEditing;
            } else {
                this.editing = !this.editing
            }

        },
        startEditing() {
            this.editing = true;
        }

        // confirmEdit(day: any) {
        //   day.editing = false;
        // },
        //
        // cancelEdit(day: any) {
        //
        // },
        //
        // editScheDay(day: any) {
        //   day.editing = true
        // }
    }
})
